<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>个人信息</el-breadcrumb-item>
        </el-breadcrumb>


        <!--卡片视图-->
        <el-card class="box-card">
            <div class="demo-block">
                <!--<el-tag >设置我的资料</el-tag>-->
                <div class="source1">设置我的资料</div>
                <div class="source">
                    <el-form :label-position="labelPosition" label-width="100px" :model="loginUserForm"
                             ref="loginUserFormref" :rules="rulesedit">
                        <el-form-item label="用户名" prop="username">
                            <el-input v-model="loginUserForm.username"></el-input>
                        </el-form-item>
                        <el-form-item label="电话" prop="phone">
                            <el-input v-model="loginUserForm.phone"></el-input>
                        </el-form-item>
                        <el-form-item label="性别" prop="sex">
                            <template>
                                <el-select v-model="loginUserForm.sex" placeholder="请选择">
                                    <el-option
                                            v-for="item in allsex"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-select>
                            </template>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary"
                                       @click="toedit('loginUserFormref')">提交
                            </el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>


            <div class="demo-block" style="margin-top: 30px">
                <div class="source1">修改密码</div>
                <div class="source">
                    <el-form :model="editpassform" status-icon :rules="rules" ref="editpassformref" label-width="100px">
                        <el-form-item label="当前密码" prop="nowpass">
                            <el-input v-model.number="editpassform.nowpass" autocomplete="now-password"></el-input>
                        </el-form-item>
                        <el-form-item label="新密码" prop="pass">
                            <el-input type="password" v-model="editpassform.pass"
                                      autocomplete="new-password"></el-input>
                        </el-form-item>
                        <el-form-item label="确认密码" prop="checkPass">
                            <el-input type="password" v-model="editpassform.checkPass"
                                      autocomplete="new-password"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary"
                                       @click="toeditpass('editpassformref')">提交
                            </el-button>
                            <el-button @click="resetForm('editpassformref')">重置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>

        </el-card>
    </div>
</template>

<script>
    export default {
        name: "UserMsg",
        data: function() {
            var checkpass = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('请输入当前密码'));
                }
                this.checknowpassword(value);
                setTimeout(() => {
                    console.log(this.passcheck)
                    if (this.passcheck) {
                        callback();
                    } else {
                        return callback(new Error('密码不正确'));
                    }
                }, 800);
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.editpassform.checkPass !== '') {
                        this.$refs.editpassformref.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.editpassform.pass) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                labelPosition: 'right',
                loginUserForm: {
                    id:'',
                    username: '',
                    phone: '',
                    sex: '',
                    rolename: ''
                },
                passcheck:false,
                rulesedit:{
                    username:[
                        {required: true,message: '请输入用户'}
                    ],
                    phone:[
                        {required: true, message: '请输入电话'}
                    ],
                    sex:[
                        {required: true, message: '请选择性别'}
                    ]
                },
                allsex: [{
                    value: '男',
                    label: '男'
                }, {
                    value: '女',
                    label: '女'
                }],

                editpassform: {
                    pass: '',
                    checkPass: '',
                    nowpass: ''
                },
                rules: {
                    pass: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                    nowpass: [
                        { validator: checkpass, trigger: 'blur' }
                    ]
                }

            }
        },
        created() {
            this.getLoginUser();
        },
        methods: {
            getLoginUser(){
                this.axios.get("user/user")
                    .then(response=>{
                        this.loginUserForm = response.data;
                    })
            },

            toedit(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.axios.post("user/update",this.loginUserForm)
                            .then(response=>{
                                if(response.data.status==200){
                                    this.$message.success(response.data.msg);
                                    this.getLoginUser();
                                }else{
                                    this.$message.error(response.data.msg);
                                }
                            })
                    } else {
                        this.$message.warning('请填写完整!');
                    }
                });
            },

            checknowpassword(pass){
                this.axios.get("user/checkPassWord",{
                    params:{
                        "password":pass
                    }
                })
                    .then(response=>{
                        this.passcheck = response.data.data;
                    })
            },
            toeditpass(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        const edit = {
                            id:this.loginUserForm.id,
                            password:this.editpassform.pass
                        }
                        this.axios.post("user/update",edit)
                            .then(response=>{
                                if(response.data.status==200){
                                    this.$message.success(response.data.msg);
                                    this.resetForm('editpassformref');
                                    this.getLoginUser();
                                }else{
                                    this.$message.error(response.data.msg);
                                }
                            })
                    } else {
                        this.$message.warning('请正确填写!');
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }

        },
    }
</script>

<style scoped>
    .demo-block {
        border: 1px solid #ebebeb;
        border-radius: 3px;
        transition: .2s;
        width: auto;
    }

    .source {
        padding: 24px;
        width: 460px;
    }

    .source .el-input__inner{
        width:360px;
    }

    .source1 {
        padding: 12px 12px 5px 12px;
    }

</style>
