<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>文案管理</el-breadcrumb-item>
            <el-breadcrumb-item>在线量房</el-breadcrumb-item>
        </el-breadcrumb>
        <!--卡片视图-->
        <el-card class="box-card">
            <div style="margin-bottom: 2rem">
                <el-tag>在线量房顶部标题</el-tag>
                <template>
                    <el-table
                            border
                            stripe
                            :data="dataList1"
                            style="width: 100%">
                        <el-table-column
                                prop="text"
                                label="标题">
                        </el-table-column>
                        <el-table-column
                                width="200"
                                label="操作"
                                align="center"
                        >
                            <template slot-scope="scope">
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editV(scope.row)">编辑
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </el-card>

        <!--编辑-->
        <el-dialog
                title="编辑"
                :visible.sync="editVisible"
                width="30%"
                @close="editClose">
            <el-form :model="editForm" ref="editRef" label-width="70px">
                <el-form-item label="标题" prop="text">
                    <el-input type="textarea" v-model="editForm.text"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="toEdit">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                dataList1: [],
                editVisible: false,
                editForm: {
                    id: '',
                    text: '',
                }
            }
        },
        created() {
            this.getList1();
        },
        methods: {
            getList1() {
                this.axios.get('appointment/getAppointmentImg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        const list = [];
                        list.push(response.data.data);
                        this.dataList1 =list;
                    } else {
                        console.log(response)
                    }
                })
            },
            editV(obj) {
                this.editForm.id = obj.id;
                this.editForm.text = obj.text;
                this.editVisible = true;
            },
            toEdit() {
                this.axios.post('appointment/updateImg', this.editForm).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg);
                        this.getList1();
                        this.editVisible = false;
                    } else {
                        this.$message.error(response.data.msg);
                    }
                })
            },
            editClose() {
                this.$refs.editRef.resetFields();
            },

        }
    }
</script>

<style scoped>

</style>
