<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>文案管理</el-breadcrumb-item>
            <el-breadcrumb-item>招商加盟</el-breadcrumb-item>
        </el-breadcrumb>
        <!--卡片视图-->
        <el-card class="box-card">
            <div style="margin-bottom: 2rem">
                <el-tag>所有模块内容</el-tag>
                <template>
                    <el-table
                            border
                            stripe
                            :data="dataList1"
                            style="width: 100%"
                            row-key="id"
                            :tree-props="{children: 'childs', hasChildren: 'hasChildren'}">
                        <el-table-column
                                prop="title"
                                label="标题">
                        </el-table-column>
                        <el-table-column
                                prop="subhead"
                                label="副标题">
                        </el-table-column>
                        <el-table-column
                                prop="text"
                                label="详情">
                        </el-table-column>
                        <el-table-column
                                prop="serialNumber"
                                label="排序号">
                        </el-table-column>
                        <el-table-column
                                prop="type"
                                label="类型">
                        </el-table-column>
                        <el-table-column
                                width="200"
                                label="操作"
                                align="center"
                        >
                            <template slot-scope="scope">
                                <el-button v-if="scope.row.type=='SUPPORTTITLE' || scope.row.type=='POLICYTITLE' || scope.row.type=='FLOWTITLE'" type="success" icon="el-icon-edit" size="mini"
                                           @click="addV(scope.row)">新增
                                </el-button>
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editV(scope.row)">编辑
                                </el-button>
                                <el-button v-if="scope.row.parentId!=null" type="danger" icon="el-icon-delete" size="mini" @click="deleteV(scope.row)">
                                    删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </el-card>

        <!--新增-->
        <el-dialog
                title="新增"
                :visible.sync="addVisible"
                width="30%"
                @close="addClose">
            <el-form :model="addForm" ref="addRef" label-width="70px">
                <el-form-item label="标题" prop="title">
                    <el-input v-model="addForm.title" ></el-input>
                </el-form-item>
                <el-form-item label="副标题" prop="subhead">
                    <el-input v-model="addForm.subhead" ></el-input>
                </el-form-item>
                <el-form-item label="详情" prop="text">
                    <el-input v-model="addForm.text" ></el-input>
                </el-form-item>
                <el-form-item label="排序" prop="serialNumber">
                    <el-input v-model="addForm.serialNumber" ></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="toAdd">确 定</el-button>
  </span>
        </el-dialog>

        <!--编辑-->
        <el-dialog
                title="编辑"
                :visible.sync="editVisible"
                width="30%"
                @close="editClose">
            <el-form :model="editForm" ref="editRef" label-width="70px">
                <el-form-item label="标题" prop="title">
                    <el-input v-model="editForm.title" ></el-input>
                </el-form-item>
                <el-form-item label="副标题" prop="subhead">
                    <el-input v-model="editForm.subhead" ></el-input>
                </el-form-item>
                <el-form-item label="详情" prop="text">
                    <el-input v-model="editForm.text" ></el-input>
                </el-form-item>
                <el-form-item label="排序" prop="serialNumber">
                    <el-input v-model="editForm.serialNumber" ></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="toEdit">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                dataList1: [],
                addVisible:false,
                editVisible: false,
                addForm: {
                    id: '',
                    title: '',
                    subhead: '',
                    text: '',
                    serialNumber: '',
                    parentId:''
                },
                editForm: {
                    id: '',
                    title: '',
                    subhead: '',
                    text: '',
                    serialNumber: '',
                    parentId:''
                }
            }
        },
        created() {
            this.getList1();
        },
        methods: {
            getList1() {
                this.axios.get('join/getListHasChildsByType').then(response => {
                    if (response.data.status == 200) {
                        this.dataList1 =response.data.data;
                    } else {
                        console.log(response)
                    }
                })
            },
            addV(obj) {
                this.addForm.parentId = obj.id;
                this.addVisible = true;
            },
            toAdd() {
                this.axios.post('join/create', this.addForm).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg);
                        this.getList1();
                        this.addVisible = false;
                    } else {
                        this.$message.error(response.data.msg);
                    }
                })
            },
            editV(obj) {
                this.editForm.id = obj.id;
                this.editForm.title = obj.title;
                this.editForm.subhead = obj.subhead;
                this.editForm.text = obj.text;
                this.editForm.serialNumber = obj.serialNumber;
                this.editVisible = true;
            },
            toEdit() {
                this.axios.post('join/update', this.editForm).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg);
                        this.getList1();
                        this.editVisible = false;
                    } else {
                        this.$message.error(response.data.msg);
                    }
                })
            },
            deleteV(obj) {
                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('/join/delete', {params: {id: obj.id}}).then(response => {
                        if (response.data.status == 200) {
                            this.$message.success(response.data.msg);
                            this.getList1();
                            this.getList2();
                            this.getList3();
                            this.getList4();
                        } else {
                            this.$message.error(response.data.msg);
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            },
            editClose() {
                this.$refs.editRef.resetFields();
            },
            addClose() {
                this.$refs.addRef.resetFields();
            },

        }
    }
</script>

<style scoped>

</style>
