<script>
    import ElImageViewer from 'element-ui/packages/image/src/image-viewer'
    export default {
        name: "Index",
        components: { ElImageViewer },
        data: function () {
            return {
                showViewer: false, // 显示查看器
                srcList: [],
                slideList: [],
                spokespersonImg:{imgPath:''},
                showSlideTitle: true,
                descriptionMsg: [{title: ' ', text: ' '}, {title: ' ', text: ' '}, {title: ' ', text: ' '}, {
                    title: ' ',
                    text: ' '
                }, {title: ' ', text: ' '}, {title: ' ', text: ' '}],
                caseshowtitleMsg: {},
                caseshowMsg: [],
                productMsg: {},
                contactMsg: {},
                newsMsg: {},
                newsList: [],
                aboutList: [],
                aboutMsgText:[],
                caseShowImgList:[],
                productList:[],
                productImgs:[],
                active:0,
                environmentList:[]
            }
        },
        created() {
            this.getDescriptionMsgList()
            this.getSlideImg()
            this.getProductMsgList()
            this.getContactMsgList()
            this.getNewsMsgList()
            this.getIndexNews()
            this.getProductListBySeries();
            this.loginLog();
            this.getEnvironment();
            /*暂时不需要的功能*/
            /*this.getSpokespersonImg()*/
            /*this.getCaseShowImgList()*/
            /*this.getCaseshowtitleMsgList()*/
            /*this.getCaseshowMsgList()*/
            /*this.getAboutMsgList()*/
        },
        methods: {
            getEnvironment(){
                this.axios.get('multipleImg/getEnvironment').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.environmentList = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            loginLog(){
                this.axios.get('loginLog/send').then(response => {
                })
            },
            showViewerClose(){
                this.showViewer = false
            },
            getPreview(list){
                this.srcList = []
                this.srcList.push(list)
                this.srcList ? this.showViewer = true: this.$message.info('当前没有可预览的图片')
            },
            chooseProductImg(list,index){
                console.log(index)
                this.active=index;
                this.productImgs=list.childs
            },
            showAboutMsgText(index){
                this.aboutMsgText = this.aboutList[index]

            },
            getProductListBySeries() {
                this.axios.get('product/getListBySeries').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.productList = response.data.data
                        this.productImgs = this.productList[0].childs
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取轮播图照片
             */
            getSlideImg() {
                this.axios.get('index/getSlideImg').then(response => {
                    if (response.data.status == 200) {
                        this.slideList = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getCaseShowImgList() {
                this.axios.get('index/getCaseShowImg').then(response => {
                    if (response.data.status == 200) {
                        this.caseShowImgList = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },

            getSpokespersonImg() {
                this.axios.get('about/getSpokespersonImg').then(response => {
                    if (response.data.status == 200) {
                        this.spokespersonImg = response.data.data
                        console.log(this.spokespersonImg)
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取6个描述
             */
            getDescriptionMsgList() {
                this.axios.get('index/getDescriptionMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.descriptionMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取案例展示标题
             */
            getCaseshowtitleMsgList() {
                this.axios.get('index/getCaseshowtitleMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.caseshowtitleMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取案例展示详细内容
             */
            getCaseshowMsgList() {
                this.axios.get('index/getCaseshowMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.caseshowMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取产品展示标题
             */
            getProductMsgList() {
                this.axios.get('index/getProductMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.productMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取联系我们区域的内容
             */
            getContactMsgList() {
                this.axios.get('index/getContactMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.contactMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取新闻区域的标题
             */
            getNewsMsgList() {
                this.axios.get('index/getNewsMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.newsMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },

            /**
             * 获取新闻
             */
            getIndexNews() {
                this.axios.get('news/getIndexNews').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.newsList = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取新闻
             */
            getAboutMsgList() {
                this.axios.get('index/getAboutMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.aboutList = response.data.data
                        if(response.data.data.length>0){
                            this.aboutMsgText = response.data.data[0]
                        }
                    } else {
                        console.log(response)
                    }
                })
            },
        }
    }
</script>


