<template>
    <div>
        <section class="content">
            <!-- cx breadcrumb start -->
            <div class="template-section breadcrumb-area" style="background: #044f9f no-repeat scroll 50% 50% / cover;">
                <div class="container">
                    <div class="row">
                        <div class="col-sm-6">
                            <h3 class="breadcrumb-title" style="color: white">产品中心</h3>
                        </div>
                        <div class="col-sm-6">
                            <div class="breadcrumb-wrap">
                                <ul class="breadcrumb-list">
                                    <li><a href="index.html" style="color: white">首页 </a></li>
                                    <li><a href="#" style="color: white">产品中心 </a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx breadcrumb end -->
            <!-- cx team section start -->
            <div class="template-section team-area-2">
                <div class="container">
                    <div class="row">
                        <div class="col-md-3">
                            <div class="sidebar-widget">
                                <div class="sidebar-content"></div>
                                <div class="sidebar-title">
                                    <h3>产品系列</h3>
                                </div>
                                <div class="sidebar-menu">
                                    <ul>
                                        <li v-for="(value, key) in productMap" :key="key.id"><a>
                                            {{JSON.parse(key).title}}
                                        </a></li>
                                    </ul>

                                </div>

                            </div>
                        </div>
                        <el-image-viewer
                                v-if="showViewer"
                                :on-close="showViewerClose"
                                :url-list=srcList />
                        <div class="col-md-9">
                            <div class="product-box">

                                <div class="kc-product-wrap" v-for="(value, key) in productMap" :key="key.id">
                                    <div class="box-product">
                                        <div class="uk-grid uk-grid-collapse">
                                            <div class="col-md-4 img-solid">
                                                <div class="thumbnai-product">
                                                    <a>
                                                        <!--<img src="http://www.beijianggzn.com/uploadfile/2018/0919/20180919043759718.jpg">-->
                                                        <el-image
                                                                style="width: 225px;height: 225px"
                                                                :src="JSON.parse(key).imgPath"
                                                                :preview-src-list="getChildImg(value)">
                                                            <div slot="error" class="image-slot">
                                                                <i class="el-icon-picture-outline"></i>
                                                            </div>
                                                        </el-image>
                                                       <!-- <img :src="JSON.parse(key).imgPath" style="width: 225px;height: 225px">-->
                                                    </a>
                                                </div>
                                            </div>
                                            <div class="col-md-8">
                                                <div class="conten-product">
                                                    <h3>
                                                        {{JSON.parse(key).title}}
                                                    </h3>
                                                    <p>
                                                        <span class="uk-icon-star"></span>
                                                        <span class="uk-icon-star"></span>
                                                        <span class="uk-icon-star"></span>
                                                        <span class="uk-icon-star"></span>
                                                        <span class="uk-icon-star"></span>
                                                    </p>
                                                    <p class="text-padding">
                                                        {{JSON.parse(key).text}}
                                                    </p>
                                                    <a href="javascript:void(0);" class="kc-button kc-button-primary" @click="getPreview(getChildImg(value))">
                                                        实景效果
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="product-sale">新品</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx team section end -->
        </section>
    </div>

</template>

<script>
    import ElImageViewer from 'element-ui/packages/image/src/image-viewer'
    export default {
        name: "product",
        components: { ElImageViewer },
        data: function () {
            return {
                showViewer: false, // 显示查看器
                productMap: [],
                topBackground: {imgPath: ''},
                srcList: []
            }
        },
        created() {
            this.getMapBySeries();
            this.getTopBackground();
        },
        methods: {
            showViewerClose(){
                this.showViewer = false
            },
            getPreview(list){
                this.srcList = []
                this.srcList = list
                this.srcList ? this.showViewer = true: this.$message.info('当前没有可预览的图片')
            },
            getChildImg(value){
                const imgList = []
                for (let obj of value) {
                    imgList.push(obj.imgPath)
                }
                return imgList;
            },
            /**
             * 获取顶部背景图片
             */
            getTopBackground() {
                this.axios.get('multipleImg/getTopBackground').then(response => {
                    if (response.data.status == 200) {
                        this.topBackground = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getMapBySeries() {
                this.axios.get('product/getMapBySeries').then(response => {
                    if (response.data.status == 200) {
                        //console.log(response.data.data)
                        this.productMap = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },

        }
    }
</script>

<style scoped>

</style>

<style src="@/css/views/product.css" scoped>

</style>
