<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>页面控制</el-breadcrumb-item>
            <el-breadcrumb-item>前台页面控制</el-breadcrumb-item>
        </el-breadcrumb>

        <!--卡片视图-->
        <el-card class="box-card">
            <!--<el-row :gutter="20">
                <el-col :span="7">
                    <el-input clearable @clear="getUserList" placeholder="请输入内容" v-model="queryInfo.query"
                              class="input-with-select">
                        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6">
                    <el-button type="primary" @click="addUserVisible=true">添加页面</el-button>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple"></div>
                </el-col>
                <el-col :span="5">
                    <div class="grid-content bg-purple"></div>
                </el-col>
            </el-row>-->
            <el-tag >页面控制</el-tag>
            <template>
                <el-table
                        border
                        stripe
                        :data="list"
                        style="width: 100%">
                    <el-table-column
                            type="index">
                    </el-table-column>
                    <el-table-column
                            prop="router"
                            label="页面路径">
                    </el-table-column>
                    <el-table-column
                            prop="text"
                            label="描述">
                    </el-table-column>
                    <el-table-column
                            prop="isShow"
                            label="是否显示">
                        <template slot-scope="scope">
                            {{scope.row.isShow?'显示':'不显示'}}
                        </template>
                    </el-table-column>
                    <el-table-column
                            width="180"
                            label="操作"
                            align="center"
                    >
                        <template slot-scope="scope">
                            <el-button type="success" size="mini"
                                       :disabled="scope.row.isShow"
                                       @click="updateIsShow(scope.row.id,true)">开启
                            </el-button>
                            <el-button type="danger" size="mini"
                                       :disabled="scope.row.isShow?false:true"
                                       @click="updateIsShow(scope.row.id,false)">关闭
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
        </el-card>
    </div>
</template>

<script>
    export default {
        name: "Users",
        data() {
            return {
                list: [],
            }
        },
        created() {
            this.getAll();
        },
        methods: {
            getAll() {
                this.axios.get('pagecontrol/getAll').then(response => {
                    if (response.data.status == 200) {
                        this.list = response.data.data
                        console.log(response.data.data)
                    } else {
                        console.log(response.data.msg)
                    }
                })
            },
            updateIsShow(id,flag){
                const edit = {
                    id:id,
                    isShow:flag
                }
                this.axios.post('pagecontrol/update',edit).then(response => {
                    if (response.data.status == 200) {
                        this.getAll()
                        this.$message.success(response.data.msg)
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
            },
        },
    }
</script>

