<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>图片管理</el-breadcrumb-item>
            <el-breadcrumb-item>首页</el-breadcrumb-item>
        </el-breadcrumb>
        <!--卡片视图-->
        <el-card class="box-card">
            <div style="margin-bottom: 2rem">
                <el-tag>首页轮播图</el-tag>
                <el-button style="margin-left: 1rem" type="success" size="medium" @click="addV('INDEXSLIDE')">新增
                </el-button>
                <template>
                    <el-table
                            border
                            :data="dataList1"
                            style="width: 100%">
                        <el-table-column
                                prop="serialNumber"
                                label="排序号">
                        </el-table-column>
                        <el-table-column
                                prop="imgPath"
                                label="照片">
                            <template slot-scope="scope">
                                <img style="width: 100%" :src="scope.row.imgPath"/>
                            </template>
                        </el-table-column>
                        <el-table-column
                                width="300"
                                label="操作"
                                align="center"
                        >
                            <template slot-scope="scope">
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editV(scope.row)">编辑
                                </el-button>
                                <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteV(scope.row)">
                                    删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>

            <!--<div style="margin-bottom: 2rem">
                <el-tag>产品展示图</el-tag>
                &lt;!&ndash;<el-button style="margin-left: 1rem" type="success" size="medium" @click="addV('INDEXSLIDE')">新增
                </el-button>&ndash;&gt;
                <template>
                    <el-table
                            border
                            :data="dataList2"
                            style="width: 100%">
                        <el-table-column
                                prop="serialNumber"
                                label="排序号">
                        </el-table-column>
                        <el-table-column
                                prop="imgPath"
                                label="照片">
                            <template slot-scope="scope">
                                <img style="width: 100%" :src="scope.row.imgPath"/>
                            </template>
                        </el-table-column>
                        <el-table-column
                                width="300"
                                label="操作"
                                align="center"
                        >
                            <template slot-scope="scope">
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editV(scope.row)">编辑
                                </el-button>
                                &lt;!&ndash;<el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteV(scope.row)">
                                    删除
                                </el-button>&ndash;&gt;
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>-->
        </el-card>

        <!--新增-->
        <el-dialog
                title="新增"
                :visible.sync="addVisible"
                width="70%"
                @close="addClose">
            <el-row :gutter="20">
                <el-col :span="15">
                    <div class="grid-content bg-purple" style="width: 300px;margin-left: 5rem">
                        <div class="input_none">
                        <el-upload
                                class="upload-demo"
                                action="api/image/upload?filePath=index"
                                :on-change="handleChange"
                                :file-list="fileList"
                                :on-success="uploadSuccess">
                            <el-button style="display: flex;justify-content: left;margin-bottom: 1rem" size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                            <img style="width: 100%" v-if="addForm.alliImgPath"
                                 :src="addForm.alliImgPath"/>
                        </el-upload>
                        </div>
                    </div>
                </el-col>
                <el-col :span="9">
                    <div class="grid-content bg-purple" style="width: 300px">
                        <el-form :model="addForm" ref="addRef" label-width="45px">
                            <el-form-item label="排序" prop="serialNumber">
                                <el-input v-model="addForm.serialNumber"></el-input>
                            </el-form-item>
                        </el-form>
                    </div>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="toAdd">确 定</el-button>
  </span>
        </el-dialog>

        <!--编辑-->
        <el-dialog
                title="编辑"
                :visible.sync="editVisible"
                width="70%"
                @close="editClose">
            <el-row :gutter="20">
                <el-col :span="13">
                    <div class="grid-content bg-purple" style="width: 300px;margin-left: 5rem">
                        <div class="input_none">
                            <el-upload
                                    class="upload-demo"
                                    action="api/image/upload?filePath=index"
                                    :on-change="handleChange"
                                    :file-list="fileList"
                                    :on-success="uploadSuccessE">
                                <el-button style="display: flex;justify-content: left;margin-bottom: 1rem" size="small" type="primary">点击上传</el-button>
                                <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                                <img style="width: 100%" v-if="editForm.alliImgPath"
                                     :src="editForm.alliImgPath"/>
                            </el-upload>
                        </div>
                    </div>
                </el-col>
                <el-col :span="11">
                    <div class="grid-content bg-purple" style="width: 300px">
                        <el-form :model="editForm" ref="editRef" label-width="45px">
                            <el-form-item label="排序" prop="serialNumber">
                                <el-input v-model="editForm.serialNumber"></el-input>
                            </el-form-item>
                        </el-form>
                    </div>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="toEdit">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                dataList1: [],
                dataList2: [],
                addVisible: false,
                editVisible: false,
                addForm: {
                    id: '',
                    serialNumber: '',
                    imgPath: '',
                    alliImgPath: '',
                    type: '',
                },
                editForm: {
                    id: '',
                    serialNumber: '',
                    imgPath: '',
                    alliImgPath: '',
                    type: '',
                },
                fileList: []

            }
        },
        created() {
            this.getList1();
        },
        methods: {
            uploadSuccess(res,file){
                if(res.status==200){
                    console.log(res.data.allPath)
                    this.addForm.imgPath = res.data.path;
                    this.addForm.alliImgPath = res.data.allPath;
                }else{
                    this.$message.error(res.msg);
                }
                console.log(res);
            },
            uploadSuccessE(res,file){
                if(res.status==200){
                    console.log(res.data.allPath)
                    this.editForm.imgPath = res.data.path;
                    this.editForm.alliImgPath = res.data.allPath;
                }else{
                    this.$message.error(res.msg);
                }
                console.log(res);
            },
            handleChange(file, fileList) {
                this.fileList = fileList.slice(-1);
            },
            getList1() {
                this.axios.get('index/getSlideImg').then(response => {
                    if (response.data.status == 200) {
                        this.dataList1 = response.data.data;
                    } else {
                        console.log(response)
                    }
                });
               /* this.axios.get('index/getCaseShowImg').then(response => {
                    if (response.data.status == 200) {
                        this.dataList2 = response.data.data;
                    } else {
                        console.log(response)
                    }
                })*/
            },
            addV(obj) {
                this.addForm = {
                    id: '',
                    serialNumber: '',
                    imgPath: '',
                    alliImgPath: '',
                    type: '',
                };
                this.addForm.type = obj;
                this.addVisible = true;
            },
            toAdd() {
                this.axios.post('index/createImg', this.addForm).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg);
                        this.getList1();
                        this.addVisible = false;
                    } else {
                        this.$message.error(response.data.msg);
                    }
                })
            },
            editV(obj) {
                this.editForm.id = obj.id;
                this.editForm.serialNumber = obj.serialNumber;
                this.editForm.alliImgPath = obj.imgPath;
                this.editVisible = true;
            },
            toEdit() {
                this.axios.post('index/updateImg', this.editForm).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg);
                        this.getList1();
                        this.editVisible = false;
                    } else {
                        this.$message.error(response.data.msg);
                    }
                })
            },
            deleteV(obj) {
                this.$confirm('此操作将永久删除该图片, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('index/deleteImg', {params: {id: obj.id}}).then(response => {
                        if (response.data.status == 200) {
                            this.$message.success(response.data.msg);
                            this.getList1();
                        } else {
                            this.$message.error(response.data.msg);
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            },
            editClose() {
                this.$refs.editRef.resetFields();
            },
            addClose() {
                this.$refs.addRef.resetFields();
            },

        }
    }
</script>

<style scoped>
    .input_none >>> .el-upload__input {
        display: none;
    }

</style>
