<template>
    <div>
        <section class="content">
            <div>
                <img :src="sildeImg.imgPath" style="width: 100%;min-height: 10rem">
            </div>

            <div class="app-template-section team-area-2">
                <div class="container app-container">
                    <div class="row">
                        <div class="join-about">
                            <div class="join-about-all">
                                <div class="col-md-6 join-about-left">
                                    <div class="join-about-left-title">
                                        <h2>{{aboutMsg.title}}</h2>
                                        <span>{{aboutMsg.subhead}}</span>
                                    </div>
                                    <div>
                                        <div class="join-about-left-center"></div>
                                    </div>
                                    <div class="join-about-left-text aboutMsg" >
                                        {{aboutMsg.text}}
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <div style="margin-top: 3rem;float: right">
                                        <img :src="aboutImg.imgPath" style="width: 100%;height: auto;max-height: 26rem;max-width: 52rem">
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>

            <!--全方位支持开始-->
            <div style="padding: 70px 0px 0px 0px">
                <section id="our-services" class="padding ptb-xs-60 text-center">
                    <div class="container">
                        <div class="row pb-60">
                            <div class="col-sm-12 mb-20">
                                <div class="area-heading text-center">
                                    <h2 class="area-title">{{supporTtitleMsg.title}}</h2>
                                </div>
                            </div>
                        </div>
                        <div class="row">

                            <div class="supportMsgClass" v-if="supportMsgList[0]">
                             <div class="col-md-4 col-sm-6"
                                  v-for="(item,index) in supportMsgList" :key="item.id">
                                 <div class="featured-item feature-bg-box gray-bg mb-100">
                                     <div class="icon">
                                         <i :class="((index)%2) == 0?'el-icon-coin':'el-icon-coin sub-color'"></i>
                                     </div>
                                     <div class="title text-uppercase">
                                         <h4>{{item.title}}</h4>
                                     </div>
                                     <div class="desc">
                                         <h1>{{item.subhead}}</h1>
                                         <h2>{{item.text}}</h2>
                                     </div>
                                 </div>
                             </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
            <!--全方位支持结束-->
            <!--零加盟费用开始-->
            <div style="padding: 80px 0px 80px 0px" class="gray-bg">
                <section id="services">
                    <div class="container">
                        <div class="section-header text-center area-heading area-heading-gift">
                            <h2 class="area-title  wow fadeInDown">{{policyTitleMsg.title}}</h2>
                        </div>
                        <div class="row">
                            <div class="features">
                                <div class="col-md-4 col-sm-6 wow fadeInUp" data-wow-delay="0ms" v-if="policyMsgList[0]"
                                     v-for="(item,index) in policyMsgList" :key="item.id"
                                     :data-wow-duration="index*100+'ms'">
                                    <div class="media service-box">
                                        <div class="pull-left">
                                            <i class="fa fa-gift"></i>
                                        </div>
                                        <div class="media-body">
                                            <h4 class="media-heading-gift" style="width: 100%;height: 5rem">{{item.text}}</h4>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div><!--/.row-->
                    </div><!--/.container-->
                </section>
            </div>
            <!--涂典获得的证书结束-->
            <!--涂典资质开始-->
            <div style="padding: 50px 0px">
                <section class="portfolio-id">
                    <div class="container">
                        <div class="section-header text-center">
                            <h2 class="area-title wow fadeInDown">{{sceneMsg.title}}</h2>
                            <h3 class="wow fadeInDown">{{sceneMsg.subhead}}</h3>
                            <p class="text-center wow fadeInUp">{{sceneMsg.text}}</p>
                        </div>

                        <div class="portfolio-items" v-if="sceneImgList[0]">

                            <div class="portfolio-item creative" v-for="item in sceneImgList" :key="item.id">
                                <div class="portfolio-item-inner">
                                    <img class="img-responsive" :src="item.imgPath" alt="">
                                    <div class="portfolio-info">
                                        <h3>开业现场</h3>
                                    </div>
                                </div>
                            </div><!--/.portfolio-item-->

                        </div>
                    </div><!--/.container-->
                </section>
            </div>
            <!--涂典资质结束-->
            <!--加盟流程开始-->
            <div>
                <section id="work-process">
                    <div class="container ">
                        <div class="area-heading text-center">
                            <h2 class=" area-title fadeInDown" style="color: white">{{flowTitleMsg.title}}</h2>
                        </div>

                        <div class="row text-center">
                            <div class="col-md-3 col-md-4 col-xs-6" v-if="flowMsgList[0]"
                                 v-for="(item,index) in flowMsgList" :key="item.id">
                                <div class="wow fadeInUp flow-msg" data-wow-duration="400ms"
                                     :data-wow-delay="(index*100)+'ms'">
                                    <div class="icon-circle">
                                        <span>1</span>
                                        <i class="fa fa-bullhorn fa-2x"></i>
                                    </div>
                                    <h3>{{item.text}}</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
            <!--加盟流程结束-->

            <div class="template-section subscribe-area gray-bg">
                <div class="container">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="area-heading text-center">
                                <h2 class="area-title">加盟申请</h2>
                                <p>Join Apply. </p>
                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-md-12">
                            <div class="subscribe-button text-center">
                                <form class="subscribe-form">
                                    <div>
                                        <input type="text" class="subscribe-control" placeholder="您的姓名:"
                                               v-model="letterMsg.name">
                                        <input type="text" class="subscribe-control" placeholder="联系电话:"
                                               v-model="letterMsg.phone">
                                        <input type="text" class="subscribe-control" placeholder="地址信息:"
                                               v-model="letterMsg.address">
                                        <textarea type="text" class="subscribe-control-message" placeholder="备注\留言:"
                                                  v-model="letterMsg.text"></textarea>
                                    </div>
                                    <div class="input-group">
                                        <button class="email-submit-btn" @click="sendMsg()">立即加入<i
                                                class="fa fa-paper-plane"></i></button>
                                    </div>
                                    <!---->
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx breadcrumb end -->
            <!-- cx team section start -->

            <!-- cx team section end -->
        </section>
    </div>

</template>

<script>

    export default {
        name: "join",
        data: function () {
            return {
                joinMsgMap: [],
                joinImgMap: [],

                aboutMsg: {title: '', subhead: '', text: ''},
                supporTtitleMsg: {title: '', subhead: '', text: ''},
                policyTitleMsg: {title: '', subhead: '', text: ''},
                sceneMsg: {title: '', subhead: '', text: ''},
                flowTitleMsg: {title: '', subhead: '', text: ''},
                supportMsgList: [], //----
                policyMsgList: [],
                flowMsgList: [],

                sildeImg: {imgPath: ''},
                aboutImg: {imgPath: ''},
                sceneImgList: [],
                letterMsg: {name: '', phone: '', address: '', text: '', ipAddress: ''}
            }
        },
        created() {
            /*this.getMsgListByType()*/
            /*this.getImgListByType()*/
            this.getSlideImg();
            this.getSceneImg();
            this.getAboutImg();

            this.getAboutMsg();
            this.getSupporTtitleMsg();
            this.getPolicyTitleMsg();
            this.getSceneMsg();
            this.getFlowTitleMsg();
            this.getSupportMsg();
            this.getPolicyMsg();
            this.getFlowMsg()
        },
        methods: {
            getAboutMsg() {
                this.axios.get('join/getAboutMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.aboutMsg = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getSupporTtitleMsg() {
                this.axios.get('join/getSupporTtitleMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.supporTtitleMsg = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getPolicyTitleMsg() {
                this.axios.get('join/getPolicyTitleMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.policyTitleMsg = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getSceneMsg() {
                this.axios.get('join/getSceneMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.sceneMsg = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getFlowTitleMsg() {
                this.axios.get('join/getFlowTitleMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.flowTitleMsg = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getSupportMsg() {
                this.axios.get('join/getSupportMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.supportMsgList = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getPolicyMsg() {
                this.axios.get('join/getPolicyMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.policyMsgList = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getFlowMsg() {
                this.axios.get('join/getFlowMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.flowMsgList = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },


            getSceneImg() {
                this.axios.get('join/getSceneImg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.sceneImgList = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getAboutImg() {
                this.axios.get('join/getAboutImg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.aboutImg = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },

            getSlideImg() {
                this.axios.get('join/getSlideImg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.sildeImg = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getMsgListByType() {
                this.axios.get('join/getMsgListByType').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.joinMsgMap = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getImgListByType() {
                this.axios.get('join/getImgListByType').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.joinImgMap = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 发送留言
             */
            sendMsg() {
                this.axios.post('join/sendJoinLetterMsg', this.letterMsg).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg)
                    } else {
                        this.$message.error(response.data.msg)
                    }
                    this.letterMsg = {name: '', phone: '', address: '', text: '', ipAddress: ''}
                })
            }
        }
    }
</script>

<style scoped>

</style>

<style src="@/css/views/join.css" scoped>

</style>
