<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        </el-breadcrumb>

        <!--卡片视图-->
        <el-card class="box-card">
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-input clearable @clear="getUserList" placeholder="请输入内容" v-model="queryInfo.query"
                              class="input-with-select">
                        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6">
                    <el-button type="primary" @click="addUserVisible=true">添加用户</el-button>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple"></div>
                </el-col>
                <el-col :span="5">
                    <div class="grid-content bg-purple"></div>
                </el-col>
            </el-row>

            <template>
                <el-table
                        border
                        stripe
                        :data="userList"
                        style="width: 100%">
                    <el-table-column
                            type="index">
                    </el-table-column>
                    <el-table-column
                            prop="username"
                            label="姓名">
                    </el-table-column>
                    <el-table-column
                            prop="sex"
                            label="性别">
                    </el-table-column>
                    <el-table-column
                            prop="phone"
                            label="电话">
                    </el-table-column>
                    <!-- <el-table-column
                             prop="role_name"
                             label="角色">
                     </el-table-column>-->
                    <el-table-column
                            prop="state"
                            label="状态">
                        <template slot-scope="scope">
                            {{scope.row.state==1?'正常':scope.row.state==2?'废除':'锁定'}}
                        </template>
                    </el-table-column>
                    <el-table-column
                            width="180"
                            label="操作"
                            align="center"
                    >
                        <template slot-scope="scope">
                            <el-button type="success" icon="el-icon-edit" size="mini" @click="editUser(scope.row)">编辑
                            </el-button>
                            <el-button type="danger" icon="el-icon-delete" size="mini"
                                       @click="deleteUser(scope.row.id)">删除
                            </el-button>
                            <!--<el-tooltip  effect="dark" content="分配角色" placement="top" :enterable="false" >
                                <el-button type="primary" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
                            </el-tooltip>-->

                        </template>
                    </el-table-column>
                </el-table>

                <!--<el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="queryInfo.pagenum"
                        :page-sizes="[1, 2, 5, 10]"
                        :page-size="queryInfo.pagesize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                </el-pagination>-->
            </template>
        </el-card>

        <!--<el-dialog
                title="修改角色"
                :visible.sync="setUserVisible"
                width="30%"
                >
            <div>
                <p>当前的用户: {{userInfo.username}}</p>
                <p>当前的角色: {{userInfo.role_name}}</p>
                <p>角色:
                    <el-select v-model="selectRoleList" placeholder="请选择">
                        <el-option
                                v-for="item in roleList"
                                :key="item.id"
                                :label="item.roleName"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </p>
            </div>


            <span slot="footer" class="dialog-footer">
    <el-button @click="setUserVisible = false">取 消</el-button>
    <el-button type="primary" @click="changeRole">确 定</el-button>
  </span>
        </el-dialog>-->

        <!--添加用户对话框-->
        <el-dialog
                title="添加用户"
                :visible.sync="addUserVisible"
                width="30%"
                @close="addUserClose">
            <el-form :model="addUserForm" :rules="addUserRules" ref="addUserRef" label-width="70px">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="addUserForm.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="addUserForm.password"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-input v-model="addUserForm.sex"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="phone">
                    <el-input v-model="addUserForm.phone"></el-input>
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
    <el-button @click="addUserVisible = false">取 消</el-button>
    <el-button type="primary" @click="toAddUser">确 定</el-button>
  </span>
        </el-dialog>
        <el-dialog
                title="修改用户"
                :visible.sync="editUserVisible"
                width="30%"
                @close="editUserClose">
            <el-form :model="editUserForm" :rules="addUserRules" ref="editUserRef" label-width="70px">
                <el-form-item label="用户名">
                    <el-input v-model="editUserForm.username"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-input v-model="editUserForm.sex"></el-input>
                </el-form-item>
                <el-form-item label="电话" prop="mobile">
                    <el-input v-model="editUserForm.phone"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="editUserForm.password"></el-input>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select v-model="editUserForm.state" placeholder="请选择" style="width: 100%">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
    <el-button @click="editUserVisible = false">取 消</el-button>
    <el-button type="primary" @click="toEditUser">确 定</el-button>
  </span>
        </el-dialog>

    </div>
</template>

<script>
    export default {
        name: "Users",
        data() {
            return {
                options: [{
                    value: "1",
                    label: '正常'
                }, {
                    value: "2",
                    label: '废除'
                }, {
                    value: "3",
                    label: '锁定'
                }],
                addUserForm: {
                    username: '',
                    password: '',
                    sex: '',
                    phone: '',
                },
                editUserForm: {
                    id: '',
                    username: '',
                    password: '',
                    sex: '',
                    phone: '',
                    state: '',
                },
                addUserRules: {
                    username: [
                        {required: true, message: '请输入用户名', trigger: 'blur'},
                    ],
                    password: [
                        {required: true, message: '请输入密码', trigger: 'blur'},
                    ],
                    sex: [
                        {required: true, message: '请输入性别', trigger: 'blur'},
                    ],
                    phone: [
                        {required: true, message: '请输入手机', trigger: 'blur'},
                    ],
                },
                addUserVisible: false,
                editUserVisible: false,
                /*setUserVisible:false,*/
                queryInfo: {
                    query: '',
                    /* pagenum:1,
                     pagesize:5*/
                },
                userList: [],
                /*total:0,*/
                /*userInfo:{},*/
                /*roleList:[],*/
                /*selectRoleList:'',*/
            }
        },
        created() {
            this.getUserList();
        },
        methods: {
            getUserList() {
                this.axios.get('/user/getList', {params: {username: this.queryInfo.query}}).then(response => {
                    console.log(response);
                    if (response.data.status == 200) {
                        this.userList = response.data.data;
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
            },
            /*changeRole(){
                if(this.selectRoleList==''){
                    this.$message.warning("请选择角色");
                }else {
                    this.axios.put('users/' + this.userInfo.id + '/role', {
                        rid: this.selectRoleList
                    }).then(response => {
                        if (response.data.meta.status == 200) {
                            this.$message.success(response.data.meta.msg);
                            this.getUserList();
                            this.setUserVisible = false;
                        } else {
                            this.$message.error(response.data.meta.msg);
                        }
                    })
                }
            },
            setRole(userInfo){
                this.userInfo = userInfo;
                this.selectRoleList = '';
                this.axios.get('roles').then(response=>{
                    if(response.data.meta.status==200){
                        this.roleList = response.data.data;
                    }else{
                        this.$message.error(response.data.meta.msg);
                    }
                })
                this.setUserVisible = true;

            },*/
            editUser(obj) {
                /*this.axios.get('users/' + id).then(response=>{
                    if(response.data.meta.status==200){
                        this.editUserForm = response.data.data;
                    }else{
                        this.$message.error(response.data.meta.msg);
                    }
                })
*/
                this.editUserForm.id = obj.id;
                this.editUserForm.username =  obj.username;
                this.editUserForm.password = '';
                this.editUserForm.sex =  obj.sex;
                this.editUserForm.phone =  obj.phone;
                this.editUserForm.state =  obj.state;
                console.log(this.editUserForm)
                this.editUserVisible = true
            },
            toAddUser() {
                this.$refs.addUserRef.validate(valid => {
                    if (valid) {
                        this.axios.post('user/create', this.addUserForm).then(response => {
                            if (response.data.status == 200) {
                                this.$message.success(response.data.msg);
                                this.getUserList();
                                this.addUserVisible = false;
                            } else {
                                this.$message.error(response.data.msg);
                            }
                        })

                    }
                })
            },
            toEditUser() {
                this.$refs.editUserRef.validate(valid => {
                    if (valid) {
                        this.axios.post('user/update', this.editUserForm).then(response => {
                            if (response.data.status == 200) {
                                this.$message.success(response.data.msg);
                                this.getUserList();
                                this.editUserVisible = false;
                            } else {
                                this.$message.error(response.data.msg);
                            }
                        })

                    }
                })
            },
            addUserClose() {
                this.$refs.addUserRef.resetFields();
            },
            editUserClose() {
                this.$refs.editUserRef.resetFields();
            },
            deleteUser(id) {
                this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('/user/delete', {params: {id: id}}).then(response => {
                        if (response.data.status == 200) {
                            this.$message.success(response.data.msg);
                            this.getUserList();
                        } else {
                            this.$message.error(response.data.msg);
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            /*handleCurrentChange(current){
                this.queryInfo.pagenum = current;
                this.getUserList();
            },
            handleSizeChange(size){
                this.queryInfo.pagesize = size;
                this.getUserList();
            },*/


        },
    }
</script>

<style scoped>


</style>
