<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>留言管理</el-breadcrumb-item>
            <el-breadcrumb-item>加盟信息</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
            <div style="margin-bottom: 13px">
                <el-button type="primary" size="small" @click="toggleSelection()">取消全选</el-button>
                <el-button type="success" size="small" @click="allToChangeState()">一键已读</el-button>
                <el-button type="danger" size="small" @click="allToDelete()">一键删除</el-button>
                <span style="float: right">

        <template>
            <font size="2px">查询日期：</font>
            <el-date-picker
                    @change="getTime()"
                    v-model="time"
                    type="daterange"
                    size="small"
                    value-format="yyyy-MM-dd"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期">
            </el-date-picker>
            &nbsp;
            &nbsp;
            <font size="2px">状态：</font>
            <el-select v-model="choosestate" placeholder="请选择" size="small" style="width: 150px" @change="getall()">
              <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
              </el-option>
            </el-select>
        </template>

    </span>
            </div>

            <template>
                <el-table
                        ref="multipleTable"
                        tooltip-effect="dark"
                        :data="tableData"
                        style="width: 100%"
                        border
                        stripe
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            prop="createTime"
                            label="日期"
                            width="180">
                        <template slot-scope="scope">
                            {{scope.row.createTime | dateFormatFull}}
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label="姓名">
                    </el-table-column>
                    <el-table-column
                            prop="phone"
                            label="电话">
                    </el-table-column>
                    <el-table-column
                            prop="address"
                            label="地址">
                    </el-table-column>
                    <el-table-column
                            prop="text"
                            label="内容">
                    </el-table-column>
                    <el-table-column
                            prop="state"
                            label="状态"
                            width="120">
                        <template slot-scope="scope">
                            <span>{{scope.row.state==2?"收藏":scope.row.state==1?"已读":"未读"}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="updateperson"
                            label="查看人"
                            width="120">
                    </el-table-column>
                    <el-table-column
                            prop="updatetime"
                            label="查看时间"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            fixed="right"
                            label="操作"
                            align="center"
                            width="230">
                        <template slot-scope="scope">
                            <el-button type="primary" icon="el-icon-chat-line-round" size="mini"
                                       @click="lookDetails(scope.$index,scope.row)">查看详情
                            </el-button>
                            <el-button type="danger" icon="el-icon-delete" size="mini"
                                       @click="deleteJoinLeaveMsg(scope.$index,scope.row)">删除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>

            </template>

            <div style="margin-top: 20px">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10, 5, 8, 15]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="totalCount">
                </el-pagination>
            </div>

            <!--查看详情-->
            <el-dialog title="详情" custom-class="detailscss" :visible.sync="lookdetailsV" append-to-body>
                <el-form :model="joinLeaveMsg" label-width="70px">
                    <el-form-item label="姓名">
                        <el-input v-model="joinLeaveMsg.name"></el-input>
                    </el-form-item>
                    <el-form-item label="电话">
                        <el-input v-model="joinLeaveMsg.phone"></el-input>
                    </el-form-item>
                    <el-form-item label="地址">
                        <el-input v-model="joinLeaveMsg.address"></el-input>
                    </el-form-item>
                    <el-form-item label="留言">
                        <el-input v-model="joinLeaveMsg.text"></el-input>
                    </el-form-item>
                </el-form>
                <!--<span>{{this.joinLeaveMsg}}</span>-->
                <div slot="footer" class="dialog-footer">
                    <el-button type="primary" size="small" @click="lookdetailsV = false">确 定</el-button>
                </div>
            </el-dialog>

        </el-card>
    </div>

</template>

<script>
    export default {
        name: "ContactUsMessage",
        data: function () {
            return {
                totalCount: 0,
                pageSize: 10,
                currentPage: 1,
                time: '', //时间
                options: [{
                    value: '',
                    label: '全部'
                }, {
                    value: 0,
                    label: '未读'
                }, {
                    value: 1,
                    label: '已读'
                }, {
                    value: 2,
                    label: '收藏'
                }],
                choosestate: '', //选择的状态
                joinLeaveMsg: {},//查看详情的文本
                lookdetailsV: false,//查看详情 显示和隐藏
                tableData: [], //首页数据集合
                multipleSelection: []
            }
        },
        created() {
            this.getAll();
        },
        methods: {
            //一键已读
            allToChangeState() {
                this.$confirm('确认改为已读吗？', '确认信息', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '确认',
                    cancelButtonText: '取消'
                })
                    .then(() => {
                        const edit = {
                            ids: this.multipleSelection
                        };
                        this.axios.post("letterMessage/changeLetterMessageStateByIds", edit)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                            })
                    })
            },
            //一键删除
            allToDelete() {
                this.$confirm('确认删除吗？', '确认信息', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '确认',
                    cancelButtonText: '取消'
                })
                    .then(() => {
                        const edit = {
                            ids: this.multipleSelection
                        };
                        this.axios.post("letterMessage/deleteLetterMessageByIds", edit)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                            })
                    })
            },
            getTime() {
                if (this.time == null) {
                    this.time = '';
                }
                this.getAll();
            },
            //查看详情
            lookDetails(index, row) {
                this.joinLeaveMsg = {};
                this.joinLeaveMsg = row;
                this.changeState(index, row);
                this.lookdetailsV = true;
            },
            //查看详情后改为已读
            changeState(index, row) {
                const edit = {
                    ids: row.id
                };
                this.axios.post("letterMessage/changeLetterMessageStateByIds", edit)
                    .then(response => {
                        if (response.data.status == 200) {
                        } else {
                            this.$message.error(response.data.msg);
                        }
                        this.getAll();
                    })
            },
            //删除信件
            deleteJoinLeaveMsg(index, row) {
                this.$confirm('确认删除该信息吗？', '确认信息', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '确认',
                    cancelButtonText: '取消'
                })
                    .then(() => {
                        const edit = {
                            ids: row.id
                        };
                        this.axios.post("letterMessage/deleteLetterMessageByIds", edit)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                            })
                    })
            },
            getAll() {
                this.axios.get("letterMessage/getLetterMessageList", {
                    params: {
                        "page": this.currentPage,
                        "size": this.pageSize,
                        "state": this.choosestate,
                        "startTime": this.time[0],
                        "endTime": this.time[1],
                    }
                })
                    .then(response => {
                        console.log(response);
                        this.tableData = response.data.data.records;
                        this.totalCount = response.data.data.total;
                    })

            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                let arr = [];
                val.forEach((item) => {
                    arr.push(item.id)
                });
                this.multipleSelection = arr;
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getAll();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getAll();
            },
        },
    }
</script>

