<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>邮箱管理</el-breadcrumb-item>
            <el-breadcrumb-item>收件邮箱设置</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
            <div>
                <el-button type="primary" icon="el-icon-circle-plus-outline" size="small" @click="addEmail()">添加收件邮箱
                </el-button>
            </div>

            <div style="margin-top: 10px">
                <template>
                    <el-table
                            border
                            stripe
                            :data="tableData"
                            style="width: 100%">
                        <el-table-column
                                prop="email"
                                label="邮箱账号"
                        >
                        </el-table-column>
                        <el-table-column
                                prop="remark"
                                label="备注"
                        >
                        </el-table-column>
                        <el-table-column
                                prop="updateTime"
                                label="更新时间"
                        >
                        </el-table-column>
                        <el-table-column
                                prop="state"
                                label="状态">
                            <template slot-scope="scope">
                                <span>{{scope.row.state?"正常":"禁用"}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                                fixed="right"
                                label="操作"
                                align="center"
                                width="300">
                            <template slot-scope="scope">
                                <el-button type="warning" icon="el-icon-circle-close" size="mini"
                                           v-if="scope.row.state==true" @click="changeState(scope.row,false)">禁用
                                </el-button>
                                <el-button type="success" icon="el-icon-circle-check" size="mini"
                                           v-if="scope.row.state==false" @click="changeState(scope.row,true)">启用
                                </el-button>
                                <el-button type="primary" icon="el-icon-edit" size="mini"
                                           @click="editEmail(scope.$index,scope.row)">修改
                                </el-button>
                                <el-button type="danger" icon="el-icon-delete" size="mini"
                                           @click="deleteEmail(scope.$index,scope.row)">删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>

                <!--添加邮箱-->
                <el-dialog title="添加邮箱账号" custom-class="EmailAdd" :visible.sync="addemailVisible" append-to-body>
                    <el-form :inline="true" :model="addemailForm" label-width="100px" ref="addemailForm" :rules="rules">
                        <el-form-item label="邮箱账号：" prop="email">
                            <el-input v-model.trim="addemailForm.email"></el-input>
                        </el-form-item>
                        <el-form-item label="备注：" prop="remark">
                            <el-input v-model.trim="addemailForm.remark"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="addemailVisible = false">取 消</el-button>
                        <el-button type="primary" @click="toaddEmail('addemailForm')">确 定</el-button>
                    </div>
                </el-dialog>

                <!--修改邮箱-->
                <el-dialog title="修改邮箱账号" custom-class="EmailAdd" :visible.sync="editemailVisible" append-to-body>
                    <el-form :inline="true" :model="editemailForm" label-width="100px" ref="editemailForm"
                             :rules="rules">
                        <el-form-item label="邮箱账号：" prop="email">
                            <el-input v-model.trim="editemailForm.email"></el-input>
                        </el-form-item>
                        <el-form-item label="备注：" prop="remark">
                            <el-input v-model.trim="editemailForm.remark"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="editemailVisible = false">取 消</el-button>
                        <el-button type="primary" @click="toEditEmail('editemailForm')">确 定</el-button>
                    </div>
                </el-dialog>
            </div>

        </el-card>
    </div>
</template>

<script>
    export default {
        name: "EmailMessage",
        data() {
            return {
                tableData: [],

                addemailVisible: false,

                addemailForm: {email: '', remark: ''},

                editemailVisible: false,

                editemailForm: {id: '', email: '', remark: ''},

                rules: {
                    email: [
                        {required: true, message: '请输入邮箱账号'}
                    ],
                    remark: [
                        {required: true, message: '请输入备注'}
                    ],
                },
            }
        },
        created() {
            this.getAll();
        },
        methods: {
            //获取全部用户信息
            getAll() {
                this.axios.get("emailmessage/getAll")
                    .then(response => {
                        console.log(response);
                        this.tableData = response.data.data;
                    })
            },
            //删除用户
            deleteEmail(index, row) {
                this.$confirm('确认删除该邮箱信息吗？', '确认信息', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '确认',
                    cancelButtonText: '取消'
                })
                    .then(() => {
                        this.axios.delete("emailmessage/delete", {params: {id: row.id}})
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                    this.getAll();
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                            })
                    })
            },
            //修改用户状态
            changeState(row, state) {

                this.$confirm(state ? '确认该邮箱接受新留言吗？' : '确认该邮箱不再接受新留言吗？', '确认信息', {
                    confirmButtonText: '确认',
                    cancelButtonText: '取消'
                })
                    .then(() => {
                        const edit = {
                            ids: row.id,
                            state:state
                        };
                        this.axios.post("emailmessage/changeState", edit)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                            })
                    })
            },
            addEmail() {
                this.addemailForm = {email: '', remark: ''};
                this.addemailVisible = true;
            },
            //添加邮箱
            toaddEmail(tablename) {
                this.$refs[tablename].validate((valid) => {
                    if (valid) {
                        console.log(data)
                        this.axios.post("emailmessage/create", this.addemailForm)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                                this.addemailVisible = false;
                            })
                    } else {
                        this.$message.warning('请填写完整!');
                    }
                })
            },
            editEmail(index, row) {
                this.editemailForm.id = row.id;
                this.editemailForm.remark = row.remark;
                this.editemailForm.email = row.email;
                this.editemailVisible = true;
            },
            toEditEmail(tablename) {
                this.$refs[tablename].validate((valid) => {
                    if (valid) {
                        this.axios.post("emailmessage/update", this.editemailForm)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                                this.editemailVisible = false;
                            })
                    } else {
                        this.$message.warning('请填写完整!');
                    }
                })
            }
        },
    }
</script>

<style scoped>

</style>
