import Vue from 'vue'
import VueRouter from 'vue-router'

/**
 * 前台
 */
import Entry from '../views/Entry'
import News from '../views/page/news'
import Product from '../views/page/product'
import Show from '../views/page/show'
import Join from '../views/page/join'
import About from '../views/page/about'
import AntiFake from '../views/page/antiFake'
import Appointment from '../views/page/appointment'
import Contact from '../views/page/contact'
import Index from '../views/page/index'
import NewsDetail from '../views/page/newsDetail.vue'

/**
 * 后台
 */
import Home from '../components/Home'
import Login from '../components/Login'
import Welcome from '../components/Welcome'
import Users from '../components/user/Users'
import UserMsg from '../components/user/UserMsg'
import Rights from '../components/power/Rights'
import Roles from '../components/power/Roles'
import PageControl from '../components/pageControl/PageControl'
import EmailMessage from '../components/email/EmailMessage'
import JoinLeaveMessage from '../components/leaveMessage/JoinLeaveMessage'
import ContactUsMessage from '../components/leaveMessage/ContactUsMessage'
import AppointmentMessage from '../components/leaveMessage/AppointmentMessage'
import LoginLog from '../components/LoginLog/LoginLog'
import IndexE from '../components/editor/IndexE'
import AppointmentE from '../components/editor/AppointmentE'
import ContactE from '../components/editor/ContactE'
import AboutE from '../components/editor/AboutE'
import JoinE from '../components/editor/JoinE'
import AntiFakeE from '../components/editor/AntiFakeE'
import NewsE from '../components/editor/NewsE'
import IndexImg from '../components/image/IndexImg'
import AboutImg from '../components/image/AboutImg'
import AppointMentImg from '../components/image/AppointMentImg'
import JoinImg from '../components/image/JoinImg'
import AntFikeImg from '../components/image/AntFikeImg'
import OtherImg from '../components/image/OtherImg'
import ProductImg from '../components/image/ProductImg'
import ShowImg from '../components/image/ShowImg'

Vue.use(VueRouter)

const allowAccess = []
const routes = [
    {
        path: '/', name: 'Entry', component: Entry, redirect: '/index', children: [
            {path: '/contact', component: Contact},
            {path: '/appointment', component: Appointment},
            {path: '/antiFake', component: AntiFake},
            {path: '/index', component: Index},
            {path: '/about', component: About},
            {path: '/join', component: Join},
            {path: '/news', component: News},
            {path: '/product', component: Product},
            {path: '/show', component: Show},
            {path: '/newsDetail', component: NewsDetail},
        ]
    },
    {path: '/login', component: Login},
    {
        path: '/home', component: Home, redirect: '/welcome', children: [
            {path: '/welcome', component: Welcome},
            {path: '/Users', component: Users},
            {path: '/UserMsg', component: UserMsg},
            {path: '/Rights', component: Rights},
            {path: '/Roles', component: Roles},
            {path: '/PageControl', component: PageControl},
            {path: '/EmailMessage', component: EmailMessage},
            {path: '/JoinLeaveMessage', component: JoinLeaveMessage},
            {path: '/ContactUsMessage', component: ContactUsMessage},
            {path: '/AppointmentMessage', component: AppointmentMessage},
            {path: '/LoginLog', component: LoginLog},
            {path: '/IndexE', component: IndexE},
            {path: '/AppointmentE', component: AppointmentE},
            {path: '/ContactE', component: ContactE},
            {path: '/AboutE', component: AboutE},
            {path: '/JoinE', component: JoinE},
            {path: '/AntiFakeE', component: AntiFakeE},
            {path: '/NewsE', component: NewsE},
            {path: '/IndexImg', component: IndexImg},
            {path: '/AboutImg', component: AboutImg},
            {path: '/AppointMentImg', component: AppointMentImg},
            {path: '/JoinImg', component: JoinImg},
            {path: '/AntFikeImg', component: AntFikeImg},
            {path: '/OtherImg', component: OtherImg},
            {path: '/ShowImg', component: ShowImg},
            {path: '/ProductImg', component: ProductImg},
        ]
    },
]

const router = new VueRouter({
    routes
})

// to 从哪里来 from 代表哪个路径跳转来 next 放行
router.beforeEach((to, from, next) => {
    //所有可以访问的界面
    if (allowAccess.length === 0) {
        for (let i = 0; i < routes.length; i++) {
            if (routes[i].path === '/') {
                const childs = routes[i].children;
                for (let i = 0; i < childs.length; i++) {
                    allowAccess.push(childs[i].path)
                }
            }
        }
    }
    if (allowAccess.indexOf(to.path) >= 0) {
        return next()
    }
    //登录界面
    if (to.path === '/login') {
        return next()
    }
    const tokenStr = window.sessionStorage.getItem("token")
    if (!tokenStr) {
        return next('/login')
    }
    next()
})

export default router
