<script>
    import ElImageViewer from "element-ui/packages/image/src/image-viewer";
    export default {
        name: "show",
        components: { ElImageViewer },
        data: function () {
            return {
                showViewer: false, // 显示查看器
                showMap: [],
                topBackground: {imgPath: ''},
                srcList: []
            }
        },
        created() {
            this.getMapBySeries();
            this.getTopBackground();
        },

        methods: {
            showViewerClose(){
                this.showViewer = false
            },
            getPreview(list){
                this.srcList = []
                this.srcList = list
                this.srcList ? this.showViewer = true: this.$message.info('当前没有可预览的图片')
            },
            getChildImg(value){
                const imgList = []
                for (let obj of value) {
                    imgList.push(obj.imgPath)
                }
                return imgList;
            },
            /**
             * 获取顶部背景图片
             */
            getTopBackground() {
                this.axios.get('join/getSlideImg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.topBackground = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            getMapBySeries() {
                this.axios.get('show/getMapBySeries').then(response => {
                    if (response.data.status == 200) {
                        //console.log(response.data.data)
                        this.showMap = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },

        }
    }
</script>

