<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>登录管理</el-breadcrumb-item>
            <el-breadcrumb-item>登录信息</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
            <div style="margin-bottom: 13px">
                <template>
                    <font size="2px">查询日期：</font>
                    <el-date-picker
                            @change="getTime()"
                            v-model="time"
                            type="daterange"
                            size="small"
                            value-format="yyyy-MM-dd"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                    </el-date-picker>
                    &nbsp;
                    <font size="2px">时间区间：</font>
                    <el-select v-model="choosestate" placeholder="请选择" size="small" style="width: 150px"
                               @change="getAll()">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </template>
            </div>

            <template>
                <el-table
                        ref="multipleTable"
                        tooltip-effect="dark"
                        :data="tableData"
                        style="width: 100%"
                        border
                        stripe
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            prop="loginAddress"
                            label="区域">
                    </el-table-column>
                    <el-table-column
                            prop="num"
                            label="访问次数">
                    </el-table-column>
                </el-table>

            </template>

            <div style="margin-top: 20px">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10, 5, 8, 15]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="totalCount">
                </el-pagination>
            </div>
        </el-card>
    </div>

</template>

<script>
    export default {
        name: "LoginLog",
        data: function () {
            return {
                totalCount: 0,
                pageSize: 10,
                currentPage: 1,
                time: '', //时间
                options: [{
                    value: 30,
                    label: '最近一个月'
                }, {
                    value: 60,
                    label: '最近两个月'
                }, {
                    value: 180,
                    label: '最近半年'
                }, {
                    value: 365,
                    label: '最近一年'
                }, {
                    value: '',
                    label: '全部'
                }],
                choosestate: '', //选择的状态
                tableData: [], //首页数据集合
            }
        },
        created() {
            this.getAll();
        },
        methods: {
            getTime() {
                if (this.time == null) {
                    this.time = '';
                }
                this.getAll();
            },

            getAll() {
                console.log(this.time[0])
                console.log(this.time[1])
                this.axios.get("loginLog/getList", {
                    params: {
                        "page": this.currentPage,
                        "size": this.pageSize,
                        "queryDay": this.choosestate,
                        "startTime": this.time[0],
                        "endTime": this.time[1],
                    }
                })
                    .then(response => {
                        console.log(response);
                        this.tableData = response.data.data.records;
                        this.totalCount = response.data.data.total;
                    })
            },
            handleSelectionChange(val) {
                let arr = [];
                val.forEach((item) => {
                    arr.push(item.id)
                });
                this.multipleSelection = arr;
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getAll();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getAll();
            },
        },
    }
</script>

<style scoped>

</style>
