        <!--编辑-->
        <el-dialog
                title="编辑"
                :visible.sync="editVisible"
                width="30%"
                @close="editClose">
            <el-form :model="editForm" ref="editRef" label-width="70px">
                <el-form-item label="标题" prop="title">
                    <el-input v-model="editForm.title" ></el-input>
                </el-form-item>
                <el-form-item label="描述" prop="text">
                    <el-input v-model="editForm.text" ></el-input>
                </el-form-item>
                <el-form-item label="排序" prop="serialNumber">
                    <el-input v-model="editForm.serialNumber" ></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="toEdit">确 定</el-button>
  </span>
        </el-dialog>

        <!--新增-->
        <el-dialog
                title="新增"
                :visible.sync="addVisible"
                width="30%"
                @close="addClose">
            <el-form :model="addForm" ref="addRef" label-width="70px">
                <el-form-item label="标题" prop="title">
                    <el-input v-model="addForm.title" ></el-input>
                </el-form-item>
                <el-form-item label="描述" prop="text">
                    <el-input v-model="addForm.text" ></el-input>
                </el-form-item>
                <el-form-item label="排序" prop="serialNumber">
                    <el-input v-model="addForm.serialNumber" ></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="toAdd">确 定</el-button>
  </span>
        </el-dialog>

    </div>
</template>

<script>
    export default {
        name: "IndexE",
        data() {
            return {
                dataList1: [],
                dataList2: [],
                dataList3: [],
                dataList4: [],
                addVisible: false,
                editVisible: false,
                addForm: {
                    id: '',
                    title: '',
                    text: '',
                    serialNumber: '',
                    parentId: '',
                    type: '',
                },
                editForm: {
                    id: '',
                    title: '',
                    text: '',
                    serialNumber: '',
                }
            }
        },
        created() {
            this.getList1();
           /* this.getList2();*/
            this.getList3();
            /*this.getList4();*/
        },
        methods: {
            getList1() {
                this.axios.get('index/getDescriptionMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.dataList1 = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getList2() {
                this.axios.get('index/getCaseshowMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.dataList2 = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getList3() {
                this.axios.get('index/getAllTitle').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.dataList3 = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getList4() {
                this.axios.get('index/getAboutMsgList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.dataList4 = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            addV(type) {
                this.addForm.type = type;
                this.addVisible = true;
            },
            addAboutChild(obj) {
                this.addForm.parentId = obj.id;
                this.addVisible = true;
            },
            editV(obj) {
                this.editForm.id = obj.id;
                this.editForm.title = obj.title;
                this.editForm.text = obj.text;
                this.editForm.serialNumber = obj.serialNumber;
                this.editVisible = true;
            },
            toEdit() {
                this.axios.post('index/update', this.editForm).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg);
                        this.getList1();
                        this.getList2();
                        this.getList3();
                        this.getList4();
                        this.editVisible = false;
                    } else {
                        this.$message.error(response.data.msg);
                    }
                })
            },
            toAdd() {
                this.axios.post('index/create', this.addForm).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg);
                        this.getList1();
                        this.getList2();
                        this.getList3();
                        this.getList4();
                        this.addVisible = false;
                    } else {
                        this.$message.error(response.data.msg);
                    }
                })
            },
            deleteV(obj) {
                this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('/index/delete', {params: {id: obj.id}}).then(response => {
                        if (response.data.status == 200) {
                            this.$message.success(response.data.msg);
                            this.getList1();
                            this.getList2();
                            this.getList3();
                            this.getList4();
                        } else {
                            this.$message.error(response.data.msg);
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            },
            addClose() {
                this.$refs.addRef.resetFields();
            },
            editClose() {
                this.$refs.editRef.resetFields();
            },

        }
    }
</script>

