<script>
    export default {
        name: "Tail",
        data: function () {
            return {
                contactMsg:{},
                logo: {imgPath:''},
                description:""
            }
        },
        created() {
            this.getLogo()
            this.getContactMsg()
            this.getTaildescriptionMsg()

        },
        methods: {
            /**
             * 获取白色logo
             */
            getLogo() {
                this.axios.get('multipleImg/getBlackLogo').then(response => {
                    if (response.data.status == 200) {
                        this.logo = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 底部公司联系信息
             */
            getContactMsg(){
                this.axios.get('contact/getContactMsg').then(response=>{
                    if(response.data.status==200){
                        this.contactMsg = response.data.data
                    }else{
                        console.log(response)
                    }
                })
            },
            /**
             * 底部公司描述 简介
             */
            getTaildescriptionMsg(){
                this.axios.get('about/getTaildescriptionMsg').then(response=>{
                    if(response.data.status==200){
                        this.description = response.data.data.title
                    }else{
                        console.log(response)
                    }
                })
            }
        }
    }
</script>

