<template>
    <div>
        <section class="content">
            <!-- cx breadcrumb start -->
            <div class="template-section breadcrumb-area"
                 style="background: #044f9f no-repeat scroll 50% 50% / cover;">
                <div class="container">
                    <div class="row">
                        <div class="col-sm-6">
                            <h3 class="breadcrumb-title" style="color: white">企业资讯</h3>
                        </div>
                        <div class="col-sm-6">
                            <div class="breadcrumb-wrap">
                                <ul class="breadcrumb-list">
                                    <li><a href="index.html" style="color: white">首页 </a></li>
                                    <li><a href="#" style="color: white">企业资讯 </a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx breadcrumb end -->
            <!-- cx team section start -->
            <div class="template-section team-area-2 gray-bg" style="padding:50px 0">
                <div class="container  ">
                    <div class="row">
                        <el-page-header @back="goBack" content="详情页面" style="margin-left: 2rem;margin-bottom: 3rem">
                        </el-page-header>
                        <div class="col-md-3">
                            <div class="sidebar-widget">
                                <div class="sidebar-content"></div>
                                <div class="sidebar-title">
                                    <h3>为您推荐</h3>
                                </div>
                                <div class="sidebar-menu">
                                    <ul>
                                        <li v-for="item in newsMsg" :key="item.id"><a>
                                            <router-link :to="{ path: '/newsDetail',query:{news:item}}">
                                                {{item.title}}
                                            </router-link>
                                        </a></li>
                                    </ul>

                                </div>

                            </div>
                        </div>
                        <div class="col-md-9">
                            <div class="product-box-detail">
                                <div>
                                    <h1>{{news.title}}</h1>
                                    <h2>发布时间:{{news.createTime | dateFormat}} &nbsp; &nbsp;&nbsp;浏览量:100 </h2>
                                </div>
                                <div style="padding-top: 2rem">
                                    <h3 v-for="item in news.childs" :key="item.id">
                                        {{item.text}}
                                    </h3>
                                </div>

                                <div style="padding-top: 6rem">
                                    <a href="javascript:void(0);">
                                        <router-link :to="{ path: '/newsDetail',query:{news:beforeNews}}">
                                            <h4 v-if="beforeNews.title">
                                                上一新闻:{{beforeNews.title}}
                                            </h4>
                                        </router-link>
                                    </a>
                                    <a href="javascript:void(0);">
                                        <router-link :to="{ path: '/newsDetail',query:{news:nextNews}}">
                                            <h4 v-if="nextNews.title">
                                                下一新闻:{{nextNews.title}}
                                            </h4>
                                        </router-link>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx team section end -->
        </section>
    </div>

</template>

<script>
    export default {
        name: "newsDetail",
        data: function () {
            return {
                topBackground: {imgPath: ''},
                newsMsg: {},
                news: {},
                beforeNews: {},
                nextNews: {}
            }
        },

        watch: {
            // 方法1
            '$route'(to, from) { //监听路由是否变化
                this.initialize();
                this.getNewsMsgList();
            },
        },
        created() {
            this.initialize();
            this.getNewsMsgList();
            this.getTopBackground();
        },
        methods: {
            goBack(){
                this.$router.push({path:'/news'})
            },
            initialize() {
                this.news = this.$route.query.news
                this.nextNews = {}
                this.beforeNews = {}

            },
            /**
             * 获取下一篇新闻和上一篇新闻
             */
            getBeforeAndNextNews(data) {
                const id = this.news.id
                for (let i = 0; i < data.length; i++) {
                    if (data[i].id === id) {
                        if (i != 0) {
                            this.beforeNews = data[i - 1]
                        }
                        if ((i+1) < data.length) {
                            console.log("下一个",data[i + 1])
                            this.nextNews = data[i + 1]
                        }
                        break;
                    }
                }
            },
            /**
             * 获取顶部背景图片
             */
            getTopBackground() {
                this.axios.get('multipleImg/getTopBackground').then(response => {
                    if (response.data.status == 200) {
                        this.topBackground = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },

            /**
             * 获取新闻区域的标题
             */
            getNewsMsgList() {
                this.axios.get('news/getNewsList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.newsMsg = response.data.data
                        this.getBeforeAndNextNews(response.data.data)
                    } else {
                        console.log(response)
                    }
                })
            },
        }
    }
</script>

<style scoped>

</style>
<style src="@/css/views/news.css" scoped>

</style>
