<template>
    <div class="content gray-bg ">
        <!-- cx team section start -->
        <div class="app-template-section-title">
            <div class="container">
                <div class="row row-title">
                    <h2>{{apointmentImg.text}}</h2>
                </div>
            </div>
        </div>
        <div class="to-app">
            <div class="app-template-section-img" :style="{background: 'url('+apointmentImg.imgPath+')center center no-repeat'}">
            </div>
            <div class="apply_form item_2">
                <form>
                    <ul>
                        <li class="item">
                            <input type="text" placeholder="手机号码:" v-model="letterMsg.phone">
                        </li>
                        <li class="item">
                            <input type="text" placeholder="您的姓名:" v-model="letterMsg.name">
                        </li>
                        <li class="item">
                            <input type="text" placeholder="所在区域:" v-model="letterMsg.address">
                        </li>
                        <li class="item">
                            <input type="text" placeholder="房屋面积:" v-model="letterMsg.area">
                        </li>
                        <li class="item">
                            <div class="app-btn" @click="sendMsg">免费设计</div>
                        </li>
                        <li class="tip">
                            今天已有
                            <span>{{num}}</span>
                            位业主提交了需求
                        </li>
                    </ul>
                </form>

            </div>
        </div>
    </div>

</template>

<script>

    export default {
        name: "appointment",
        data: function () {
            return {
                apointmentImg:{imgPath:'',text:''},
                letterMsg:{name:'',phone:'',address:'',area:'',ipAddress:''},
                num:''
            }
        },
        created() {
            this.getAppointmentImg()
            this.getAppointmentJoinNum()
        },
        methods: {
            /**
             * 获取背景图片
             */
            getAppointmentImg(){
                this.axios.get('appointment/getAppointmentImg').then(response=>{
                    if(response.data.status==200){
                        this.apointmentImg = response.data.data
                    }else{
                        console.log(response)
                    }
                })
            },
            getAppointmentJoinNum(){
                this.axios.get('appointment/getAppointmentJoinNum').then(response=>{
                    if(response.data.status==200){
                        this.num = response.data.data
                    }else{
                        console.log(response)
                    }
                })
            },
            /**
             * 发送量房留言
             */
            sendMsg(){
                this.axios.post('appointment/sendAppointmentMsg',this.letterMsg).then(response=>{
                    if(response.data.status==200){
                        this.$message.success(response.data.msg)
                    }else{
                        this.$message.error(response.data.msg)
                    }
                    this.letterMsg = {name:'',phone:'',address:'',text:'',ipAddress:''}
                })

            }
        }
    }
</script>

<style scoped>

</style>

<style src="@/css/views/appointment.css" scoped>

</style>
