            /*this.getListByType()*/
            /*this.getListByType()
            this.getImgListByType()*/
            this.getTopBackground();
            this.getAuthenticationImgList();
            this.getPublicityimgImgList();
            this.getTitleMsg();
            this.getAuthenticationTitleMsg();
            this.getPublicityMsg();
            this.getPublicityImgTitleMsg();
