             */
            getTopBackground() {
                this.axios.get('multipleImg/getTopBackground').then(response => {
                    if (response.data.status == 200) {
                        this.topBackground = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getSpokespersonImg() {
                this.axios.get('about/getSpokespersonImg').then(response => {
                    if (response.data.status == 200) {
                        this.spokespersonImg = response.data.data
                        console.log(this.spokespersonImg)
                    } else {
                        console.log(response)
                    }
                })
            },
            showHistorMsgText(index) {
                this.historyMsgText = this.historyMsgList[index]

            },
            getTitleMsg() {
                this.axios.get('about/getTitleMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.titleMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getHistoryMsg() {
                this.axios.get('about/getHistoryMsg').then(response => {
                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.historyMsgList = response.data.data
                        if (response.data.data.length > 0) {
                            this.historyMsgText = response.data.data[0]
                        }
                    } else {
                        console.log(response)
                    }
                })
            },
            getAuthenticationTitleMsg() {
                this.axios.get('about/getAuthenticationTitleMsg').then(response => {
