                    if (response.data.status == 200) {
                        console.log(response.data.data)
                        this.aboutImgMap = response.data.data

                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 发送留言
             */
            sendMsg() {
                this.axios.post('letterMessage/sendMsg', this.letterMsg).then(response => {
                    if (response.data.status == 200) {
                        this.$message.success(response.data.msg)
                    } else {
                        this.$message.error(response.data.msg)
                    }
                    this.letterMsg = {name: '', phone: '', address: '', text: '', ipAddress: ''}
                })
            }
