<template>
    <header id="sticky-header-id" class="header-area">
        <div class="header-top-area">
            <div class="container">
                <div class="row">
                    <div class="col-sm-8 col-md-8">
                        <div class="header-top-left">
                            <ul class="email-phone">
                                <li><a href="#"><i class="fa fa-envelope"></i> Email: <span class="text-bold">{{contactMsg.email}}</span></a></li>
                                <li><a href="#"><i class="fa fa-phone"></i> Call us: <span class="text-bold">{{contactMsg.phone}}</span></a></li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-sm-4 col-md-4 hidden-xs">
                        <div class="header-top-right">
                            <ul class="header-social-menu">
                                <li><a href="#"><i class="fa fa-facebook"></i></a></li>
                                <li><a href="#"><i class="fa fa-twitter"></i></a></li>
                                <li><a href="#"><i class="fa fa-instagram"></i></a></li>
                                <li><a href="#"><i class="fa fa-google-plus"></i></a></li>
                                <li><a href="#"><i class="fa fa-linkedin-square"></i></a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="main-header-area">
            <div class="container">
                <div class="row">
                    <div class="col-md-5 col-sm-6 col-xs-6">

                        <div class="logo-wrapper">
                            <a class="logo" href="index.html"><img :src="logo.imgPath" alt="" style="width: 10rem;margin-right: 2rem">
                                <span class="hidden-xs hidden-sm" v-if="companyMsg[0]">{{companyMsg[0].text}}</span>
                            </a>
                        </div>
                    </div>
                    <div class="col-md-7 hidden-xs hidden-sm">
                        <div class="menu-area">
                            <nav class="template-menu">
                                <ul class="main-menu">
                                    <li v-for="item in pageList" :key="item.id"><router-link :to="{ path: item.router}" class="navigation">{{item.text}}</router-link></li>
                                   <!-- <li><router-link :to="{ path: '/index'}">首页</router-link></li>
                                    <li><router-link :to="{ path: '/about'}">关于我们</router-link></li>
                                    <li><router-link :to="{ path: '/appointment'}">在线量房</router-link></li>
                                    <li><router-link :to="{ path: '/join'}">招商加盟</router-link></li>
                                    <li><router-link :to="{ path: '/product'}">产品中心</router-link></li>
                                    <li><router-link :to="{ path: '/show'}">精品赏析</router-link></li>
                                    <li><router-link :to="{ path: '/news'}">新闻资讯</router-link></li>
                                    <li><router-link :to="{ path: '/antiFake'}">防伪验证</router-link></li>
                                    <li><router-link :to="{ path: '/contact'}">联系我们</router-link></li>-->
                                </ul>
                            </nav>
                        </div>
                    </div>
                    <!-- Mobile menu area start -->
                    <div class="mobile-menu-area clearfix hidden-md">
                        <a href="javascript:void(0);"  class="menu-open" >
                            <span></span>
                            <span></span>
                            <span></span>
                        </a>
                    </div>

                    <div id="menu-area" class="menu-area-phone menuClose">
                        <div class="main-menu-phone text-center">
                            <div class="menu-close float-left">X</div>
                            <nav>
                                <ul>
                                    <li v-for="item in pageList" :key="item.id" v-if="item.isShow==1"><router-link :to="{ path: item.router}" class="navigation">{{item.text}}</router-link></li>
                                 <!--   <li><router-link :to="{ path: '/about'}" class="navigation">关于我们</router-link></li>
                                    <li><router-link :to="{ path: '/appointment'}" class="navigation">在线量房</router-link></li>
                                    <li><router-link :to="{ path: '/join'}" class="navigation">招商加盟</router-link></li>
                                    <li><router-link :to="{ path: '/product'}" class="navigation">产品中心</router-link></li>
                                    <li><router-link :to="{ path: '/show'}" class="navigation">精品赏析</router-link></li>
                                    <li><router-link :to="{ path: '/news'}" class="navigation">新闻资讯</router-link></li>
                                    <li><router-link :to="{ path: '/antiFake'}" class="navigation">防伪验证</router-link></li>
                                    <li><router-link :to="{ path: '/contact'}" class="navigation">联系我们</router-link></li>-->
                                </ul>
                            </nav>
                        </div>
                    </div>
                    <!-- Mobile menu area end -->
                </div>
            </div>
        </div>
    </header>
</template>

<script>
    $(function(){
        $('.menu-open').on('click', function(){
            $('#menu-area').removeClass('menuClose').addClass('menuOpen');
        });
        $('.menu-close').on('click', function(){
            $('#menu-area').addClass('menuClose').removeClass('menuOpen');
        })
        $('.navigation').on('click', function(){
            $('#menu-area').addClass('menuClose').removeClass('menuOpen');
        })
    });
    export default {
        name: "Head",
        data: function () {
            return {
                logo: {imgPath:''},
                contactMsg:{},
                pageList:[],
                companyMsg:[],
            }
        },
        created() {
            this.getContactMsg()
            this.getLogo()
            this.getHead()
            this.getCompanyMsg()

        },
        methods: {
            getHead() {
                this.axios.get('pagecontrol/getAllShow').then(response => {
                    if (response.data.status == 200) {
                        this.pageList = response.data.data
                        console.log(this.logo)
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取logo
             */
            getLogo() {
                this.axios.get('multipleImg/getWriteLogo').then(response => {
                    if (response.data.status == 200) {
                        this.logo = response.data.data
                        console.log(this.logo)
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 获取顶部公司联系信息
             */
            getContactMsg(){
                this.axios.get('contact/getContactMsg').then(response=>{
                    if(response.data.status==200){
                        this.contactMsg = response.data.data
                    }else{
                        console.log(response)
                    }
                })
            },
            /**
             * 获取顶部公司联系信息
             */
            getCompanyMsg(){
                this.axios.get('company/getAll').then(response=>{
                    if(response.data.status==200){
                        this.companyMsg = response.data.data
                    }else{
                        console.log(response)
                    }
                })
            }
        }
    }
</script>

