<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
        </el-breadcrumb>
        <!--卡片视图-->
        <el-card class="box-card">
            <el-row>
                <el-col>
                    <el-button type="primary">添加角色</el-button>
                </el-col>
            </el-row>

            <el-table border stripe :data="roleList">
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <el-row :class="['bdbottom',i1 ===0?'bdtop':'','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                            <!--渲染一级权限-->
                            <el-col :span="5">
                                <el-tag closable @close="removeRightsById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <!--渲染二级权限-->
                            <el-col :span="19">
                                <el-row v-for="(item2,i2) in item1.children" :key="item2.id" class="vcenter">
                                    <el-col :span="6">
                                        <el-tag type="success" closable @close="removeRightsById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                                        <i class="el-icon-caret-right"></i>
                                    </el-col>
                                    <el-col :span="18">
                                        <el-tag type="warning" v-for="(item3,i3) in item2.children" :key="item3.id" closable @close="removeRightsById(scope.row,item3.id)">
                                            {{item3.authName}}
                                        </el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                            <!--渲染三级权限-->
                        </el-row>
                    </template>
                </el-table-column>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="角色名称" prop="roleName"></el-table-column>
                <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
                <el-table-column label="操作" width="300px">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" icon="el-icon-search">编辑</el-button>
                        <el-button size="mini" type="danger" icon="el-icon-delete">删除</el-button>
                        <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)">分配权限</el-button>
                    </template>
                </el-table-column>
            </el-table>

            <el-dialog
                    title="分配权限"
                    :visible.sync="setRightDialogVisible"
                    width="30%"
                    @close="setRightDialogClose"
                    >
                <el-tree ref="treeRef" :data="rightsList" :props="defaultProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys">

                </el-tree>

                <span slot="footer" class="dialog-footer">
    <el-button @click="setRightDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="changeRights">确 定</el-button>
  </span>
            </el-dialog>


        </el-card>
    </div>
</template>

<script>
    export default {
        name: "Roles",
        data(){
            return{
                defaultProps: {
                    children: 'children',
                    label: 'authName'
                },
                setRightDialogVisible:false,
                rightsList:[],
                roleList:[],
                defKeys:[],
                roleId:''
            }
        },
        created() {
            this.getRoleList()
        },
        methods:{
            changeRights(){
                /*const keys = this.$refs.treeRef.getCheckedKeys()
                keys.push(this.$refs.treeRef.getHalfCheckedNodes())*/
                //console.log(keys)
                const keys = [...this.$refs.treeRef.getCheckedKeys(),...this.$refs.treeRef.getHalfCheckedNodes()]
                const idStr = keys.join(',')
                console.log(this.roleId)
                this.axios.post('roles/'+this.roleId+'/rights',{
                    rids:idStr
                }).then(response=>{
                    if(response.data.meta.status==200){
                        this.$message.success(response.data.meta.msg);
                        this.getRoleList()
                        this.setRightDialogVisible = false

                    }else{
                        this.$message.error(response.data.meta.msg);
                    }
                })


            },
            setRightDialogClose(){
                this.defKeys = []
            },
            getdefKeys(node,arr){
                if(!node.children) {
                    return arr.push(node.id)
                }
                for (let i = 0; i < node.children.length; i++) {
                    this.getdefKeys(node.children[i],arr)
                }
            },
            showSetRightDialog(role){
                this.roleId = role.id;
                this.axios.get('rights/tree').then(response=>{
                    if(response.data.meta.status==200){
                        this.$message.success(response.data.meta.msg);
                        this.rightsList = response.data.data;
                        this.defKeys = []
                        this.getdefKeys(role,this.defKeys);
                    }else{
                        this.$message.error(response.data.meta.msg);
                    }
                })
                this.setRightDialogVisible = true;
            },
            removeRightsById(role,rightId){
                this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete('roles/'+role.id+'/rights/'+rightId).then(response=>{
                        if(response.data.meta.status==200){
                            this.$message.success(response.data.meta.msg);
                            role.children = response.data.data
                        }else{
                            this.$message.error(response.data.meta.msg);
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });

            },
            getRoleList(){
                this.axios.get('roles').then(response=>{
                    if(response.data.meta.status==200){
                        this.roleList = response.data.data;
                    }else{
                        this.$message.error(response.data.meta.msg);
                    }
                })
            }

        }
    }
</script>

<style scoped>
    .el-tag{
        margin: 7px;
    }
    .bdtop{
        border-top: 1px solid #eeeeee;
    }
    .bdbottom{
        border-bottom: 1px solid #eeeeee;
    }
    .vcenter{
        display: flex;
        align-items: center;
    }

</style>