<template>
    <div class="wrapper">
        <section class="content">
            <!-- cx breadcrumb start -->
            <div class="template-section gray-bg breadcrumb-area" :style="{background: 'rgba(0, 0, 0, 0) url('+topBackground.imgPath+') no-repeat scroll 50% 50% / cover'}">
                <div class="container">
                    <div class="row">
                        <div class="col-sm-6">
                            <h3 class="breadcrumb-title">防伪验证</h3>
                        </div>
                        <div class="col-sm-6">
                            <div class="breadcrumb-wrap">
                                <ul class="breadcrumb-list">
                                    <li><a href="index.html">首页 </a></li>
                                    <li><a href="#">防伪验证 </a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx breadcrumb end -->
            <!-- cx team section start -->
            <div class="template-section">
                <div class="container">
                    <div class="row gray-bg">
                        <div class="antFake">
                            <div>
                                <img v-if="writeLogo.imgPath" :src="writeLogo.imgPath" class="logo"/>
                            </div>
                            <div>
                                <h1>
                                    涂典防伪查询
                                </h1>
                                <h2>
                                    流程指南
                                </h2>
                            </div>
                            <div v-if="antiFakeList[0]">
                                <div v-for="(item,index) in antiFakeList" :key="item.id">
                                    <h3>
                                        {{index+1}}.{{item.text}}
                                    </h3>
                                    <h4 v-for="itemText in item.textChilds" :key="itemText.id">
                                        {{itemText.text}}
                                    </h4>
                                    <div v-if="item.imgChilds.length>0">
                                        <div v-for="itemImg in item.imgChilds" width="width:100%">
                                            <img  :src="itemImg.text" class="screenshot" >
                                        </div>

                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx team section end -->
        </section>
    </div>
</template>

<script>
    export default {
        name: "antiFake",
        data: function () {
            return {
                antiFakeList: [],
                writeLogo: {imgPath:''},
                topBackground: {imgPath: ''}
            }
        },
        created() {
            this.getTopBackground();
            this.getList();
            this.getWriteLogo()
        },
        methods: {
            /**
             * 获取顶部背景图片
             */
            getTopBackground() {
                this.axios.get('multipleImg/getTopBackground').then(response => {
                    if (response.data.status == 200) {
                        this.topBackground = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            getList() {
                this.axios.get('antiFake/getList').then(response => {
                    if (response.data.status == 200) {
                        this.antiFakeList = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },

            /**
             * 获取白色logo
             */
            getWriteLogo() {
                this.axios.get('multipleImg/getWriteLogo').then(response => {
                    if (response.data.status == 200) {
                        this.writeLogo = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            }
        }
    }
</script>

<style scoped>

</style>

<style src="@/css/views/antiFake.css" scoped>

</style>
