<template>
    <div>
        <section class="content">
            <!-- cx breadcrumb start -->
            <div class="template-section breadcrumb-area" style="background: #044f9f no-repeat scroll 50% 50% / cover;">
            <div class="container">
                    <div class="row">
                        <div class="col-sm-6">
                            <h3 class="breadcrumb-title" style="color: white">企业资讯</h3>
                        </div>
                        <div class="col-sm-6">
                            <div class="breadcrumb-wrap">
                                <ul class="breadcrumb-list">
                                    <li><a href="index.html" style="color: white">首页 </a></li>
                                    <li><a href="#" style="color: white">企业资讯 </a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx breadcrumb end -->
            <!-- cx team section start -->
            <div class="template-section team-area-2 gray-bg">
                <div class="container  ">
                    <div class="row">
                        <div class="col-md-3" >
                            <div class="sidebar-widget">
                                <div class="sidebar-content"></div>
                                <div class="sidebar-title">
                                    <h3>为您推荐</h3>
                                </div>
                                <div class="sidebar-menu">
                                    <ul>
                                        <li v-for="item in newsMsg" :key="item.id"><a>
                                            <router-link :to="{ path: '/newsDetail',query:{news:item}}">
                                            {{item.title}}
                                            </router-link>
                                        </a></li>
                                    </ul>

                                </div>

                            </div>
                        </div>
                        <div class="col-md-9">
                            <div class="product-box">
                                <ul>
                                    <li v-for="item in newsMsg" :key="item.id">
                                        <div class="kc-product-wrap">
                                            <div class="box-product">
                                                <div class="uk-grid uk-grid-collapse">
                                                    <div class="col-md-4 img-solid">
                                                        <div class="thumbnai-product">
                                                            <a>
                                                                <img :src="item.imgPath">
                                                            </a>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-8">
                                                        <div class="conten-product">
                                                            <router-link :to="{ path: '/newsDetail',query:{news:item}}">
                                                            <h1>
                                                                {{item.title}}
                                                            </h1>
                                                            </router-link>
                                                            <span class="news-conent">
															{{item.text}}
														</span>
                                                            <span class="news-date">
															{{item.createTime | dateFormat}}  {{item.author}}
														</span>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                </ul>


                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- cx team section end -->
        </section>
    </div>

</template>

<script>
    export default {
        name: "news",
        data: function () {
            return {
                topBackground: {imgPath: ''},
                newsMsg: {},
            }
        },
        created() {
            this.getTopBackground();
            this.getNewsMsgList();
        },
        methods: {
            /**
             * 获取顶部背景图片
             */
            getTopBackground() {
                this.axios.get('multipleImg/getTopBackground').then(response => {
                    if (response.data.status == 200) {
                        this.topBackground = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },

            /**
             * 获取新闻区域的标题
             */
            getNewsMsgList() {
                this.axios.get('news/getNewsList').then(response => {
                    if (response.data.status == 200) {
                        console.log(response)
                        this.newsMsg = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
        }
    }
</script>

<style scoped>

</style>
<style src="@/css/views/news.css" scoped>

</style>
