<template>
    <div class="login_contariner">
        <div class="login_box">
            <div class="avatar_box">

                <img src="../img/login.jpg">
            </div>
            <el-form  ref="loginFormRef" label-width="0px" class="login_form" :model="login_form" :rules="rules">
                <el-form-item prop="username">
                    <el-input  prefix-icon="el-icon-user" v-model="login_form.username" placeholder="初始账号admin"> </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input  prefix-icon="el-icon-lock" v-model="login_form.password" type="password" placeholder="初始密码123456"></el-input>
                </el-form-item>
                <el-form-item class="btns">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button type="info" @click="resetForm">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "login",
        data(){
            return{
                rules:{
                    username: [
                        { required: true, message: '请输入登录名称', trigger: 'blur' },
                        { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
                    ],
                    password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
                },

                login_form:{
                    username:'',
                    password:''
                },
            }
        },
        methods:{
            resetForm(){
                this.$refs.loginFormRef.resetFields();
            },
            login(){
                console.log("登录中")
                this.$refs.loginFormRef.validate((valid) => {
                    if(!valid) return;
                    this.axios.post('login',this.login_form).then(response=>{
                        if(response.data.status==200){
                            this.$message.success(response.data.msg);
                            console.log(response.data.data)
                            window.sessionStorage.setItem('token',response.data.data.token);
                            this.$router.push('/home')
                        }else{
                            this.$message.error(response.data.msg);
                        }
                    })
                })
            },
        }
    }
</script>

<style lang="less" scoped>
    .login_contariner{
        background-color: #2b4b6b;
        height: 100%;
    }
    .login_box{
        width: 450px;
        height: 300px;
        background-color: #fff;
        border-radius: 3px;
        position: absolute;
        left: 50%;
        top: 45%;
        transform: translate(-50%,-50%);
        .avatar_box{
            padding: 35px;
            position: absolute;
            left: 50%;
            transform: translate(-50%,-10%);
            img{
                width: 100%;
                height: 100%;
            }
        }
    }
    .btns{
        display: flex;
        justify-content: flex-end;
    }
    .login_form{
        position: absolute;
        bottom: 0;
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;

    }
</style>
