(function($) {
    'use strict';

    // Get the form.
    var form = $('#template-contact-form');

    // Get the messages div.
    var formMessages = $('.form-send-message');

    // Set up an event listener for the contact form.
    $(form).submit(function(e) {
        // Stop the browser from submitting the form.
        e.preventDefault();

        // Serialize the form data.
        var formData = $(form).serialize();

        // Submit the form using AJAX.
        $.ajax({
                type: 'POST',
                url: $(form).attr('action'),
                data: formData
            })
            .done(function(response) {
                // Make sure that the formMessages div has the 'success' class.
                $(formMessages).removeClass('text-danger');
                $(formMessages).addClass('text-success');

                // Set the message text.
                $(formMessages).text(response);

                // Clear the form.
                $('#template-contact-form form-control').val('');
            })
            .fail(function(data) {
                // Make sure that the formMessages div has the '.text-danger' class.
                $(formMessages).removeClass('text-success');
                $(formMessages).addClass('text-danger');

                // Set the message text.
                if (data.responseText !== '') {
                    $(formMessages).text(data.responseText);
                } else {
                    $(formMessages).text('Oops! An error occurred and your message could not be sent.');
                }
            });
    });

})(jQuery);

