                                    <input type="text" class="form-control"  placeholder="您的联系方式" v-model="letterMsg.phone" required>
                                    <input type="text" class="form-control"  placeholder="您的地址" v-model="letterMsg.address" required>
                                    <p class="form-send-message"></p>
