<script>
    import Vue from 'vue'
    import VueAMap from 'vue-amap';
    Vue.use(VueAMap);
    // 初始化vue-amap
    VueAMap.initAMapApiLoader({
        // 高德的key
        key: '6a5fa1918299925aef3a53852d8f67f4',
        // 插件集合
        plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor'],
        // 高德 sdk 版本，默认为 1.4.4
        v: '1.4.4'
    });
    export default {
        name: "contact",
        data: function () {
            return {
                topBackground: {imgPath: ''},
                contactMsg:{},
                letterMsg:{name:'',phone:'',address:'',text:'',ipAddress:''},
                center :[113.540305,23.172728],
                zoom :14,
                circles: [
                    {
                        radius: 200,
                        fillOpacity: 0.5,
                        events: {
                            click: () => {
                                alert('click');
                            }
                        }
                    }
                ]
            }
        },
        created() {
            this.getTopBackground();
            this.getContactMsg()
        },
        methods: {
            /**
             * 获取顶部背景图片
             */
            getTopBackground() {
                this.axios.get('multipleImg/getTopBackground').then(response => {
                    if (response.data.status == 200) {
                        this.topBackground = response.data.data
                    } else {
                        console.log(response)
                    }
                })
            },
            /**
             * 公司联系信息
             */
            getContactMsg(){
                this.axios.get('contact/getContactMsg').then(response=>{
                    if(response.data.status==200){
                        this.contactMsg = response.data.data
                    }else{
                        console.log(response)
                    }
                })
            },
            /**
             * 发送留言
             */
            sendMsg(){
                this.axios.post('letterMessage/sendMsg',this.letterMsg).then(response=>{
                    if(response.data.status==200){
                        this.$message.success(response.data.msg)
                    }else{
                        this.$message.error(response.data.msg)
                    }
                    this.letterMsg = {name:'',phone:'',address:'',text:'',ipAddress:''}
                })

            }
        }
    }
</script>

