<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="12">
                <el-card class="box-card-index">
                    <div>
                        <div>今日访问数量</div>
                        <div style="font-size: 20px;margin: 0.5rem 0">{{indexMsg.loginLog}}</div>
                        <div>较昨日</div>
                        <div :style="colorFilter(indexMsg.loginLogY)" class="top_index_class">
                            <i :class="indexMsg.loginLogY>0?'el-icon-top':'el-icon-bottom'">{{indexMsg.loginLogY}}%</i>
                        </div>
                    </div>
                </el-card>
            </el-col>
            <!--<el-col :span="6">
                <el-card class="box-card-index">
                    <div>
                        <div>加盟申请</div>
                        <div style="font-size: 20px;margin: 0.5rem 0">{{indexMsg.join}}</div>
                        <div>较昨日</div>
                        <div :style="colorFilter(indexMsg.joinY)" class="top_index_class">
                            <i :class="indexMsg.joinY>0?'el-icon-top':'el-icon-bottom'">{{indexMsg.joinY}}%</i>
                        </div>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card class="box-card-index">
                    <div>
                        <div>在线量房</div>
                        <div style="font-size: 20px;margin: 0.5rem 0">{{indexMsg.appointment}}</div>
                        <div>较昨日</div>
                        <div :style="colorFilter(indexMsg.appointmentY)" class="top_index_class">
                            <i :class="indexMsg.appointmentY>0?'el-icon-top':'el-icon-bottom'">{{indexMsg.appointmentY}}%</i>
                        </div>
                    </div>
                </el-card>
            </el-col>-->
            <el-col :span="12">
                <el-card class="box-card-index">
                    <div>
                        <div>联系我们</div>
                        <div style="font-size: 20px;margin: 0.5rem 0">{{indexMsg.contact}}</div>
                        <div>较昨日</div>
                        <div :style="colorFilter(indexMsg.contactY)" class="top_index_class">
                            <i :class="indexMsg.contactY>0?'el-icon-top':'el-icon-bottom'">{{indexMsg.contactY}}%</i>
                        </div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-row :gutter="20" style="margin-top: 20px">
            <el-col :span="24">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>访问地区</span>
                    </div>
                    <el-row :gutter="50">
                        <el-col :span="18">
                            <div id="areaContainer" style="height: 300px"></div>
                        </el-col>
                        <el-col :span="6">
                            <dv-scroll-ranking-board v-if="areaConfig.data.length>0" :config="areaConfig"
                                                     style="width:100%;height:300px"/>
                        </el-col>
                    </el-row>


                </el-card>
            </el-col>
        </el-row>
        <el-row :gutter="20" style="margin-top: 20px">
            <el-col :span="24">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <span>最近一周访问量</span>
                        <el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button>
                    </div>
                    <div id="container" style="height: 15rem"></div>
                </el-card>
            </el-col>
        </el-row>

    </div>

</template>

<script>
    import {Chart} from '@antv/g2';


    export default {

        name: 'Welcome',

        data() {
            return {
                indexMsg: {
                    loginLog: 0,
                    loginLogY: 0,
                    join: 0,
                    joinY: 0,
                    appointment: 0,
                    appointmentY: 0,
                    contact: 0,
                    contactY: 0,
                },
                areaList: [],
                data11: [
                    {year: '星期一', value: 3},
                    {year: '星期二', value: 4},
                    {year: '星期三', value: 3.5},
                    {year: '星期四', value: 5},
                    {year: '星期五', value: 4.9},
                    {year: '星期六', value: 6},
                    {year: '星期日', value: 7},
                ],
                areaConfig: {
                    data: []
                }
            }
        },
        async mounted() {
            /*this.visitorChart();*/
            this.areaChart();
            this.getListGroupByArea();
            this.getWeekList();
        },
        created() {
            this.getIndex();
        },
        methods: {
            colorFilter(value) {
                if (value < 0) {
                    return 'color: #cf1322'
                }
                return 'color: #3f8600'


            },
            getIndex() {
                this.axios.get('home/getIndex').then(response => {
                    if (response.data.status == 200) {
                        this.indexMsg = response.data.data;
                    } else {
                        this.$message.error(response.data.meta.msg);
                    }
                })
            },
            getListGroupByArea() {
                this.axios.get('home/getListGroupByArea').then(response => {
                    if (response.data.status == 200) {
                        this.areaList = response.data.data;
                        this.areaConfig.data = this.areaList;
                    } else {
                        this.$message.error(response.data.meta.msg);
                    }
                })
            },
            areaChart() {
                this.axios.get('home/getListGroupByArea').then(response => {
                    if (response.data.status == 200) {
                        let data = response.data.data;
                        const chart = new Chart({
                            container: 'areaContainer',
                            autoFit: true,
                            height: 500,
                        });
                        chart.data(data);
                        chart.scale('value', {
                            nice: true,
                        });
                        chart.interval().position('name*value');
                        chart.interaction('active-region');
                        chart.render();
                    } else {
                        this.$message.error(response.data.meta.msg);
                    }
                })

            },

            getWeekList() {
                this.axios.get('home/getWeekList').then(response => {
                    if (response.data.status == 200) {
                        let data = response.data.data;
                        console.log(data)
                        const chart = new Chart({
                            container: 'container',
                            autoFit: true,
                            height: 500,
                        });
                        chart.data(data);
                        chart.scale({
                            year: {
                                range: [0, 1],
                            },
                            value: {
                                min: 0,
                                nice: true,
                            },
                        });
                        chart.tooltip({
                            showCrosshairs: true, // 展示 Tooltip 辅助线
                            shared: true,
                        });
                        chart.line().position('date*num').label('num').color('#3baa16');
                        chart.point().position('date*num').color('#3baa16');
                        chart.render();
                    } else {
                        this.$message.error(response.data.meta.msg);
                    }
                })

            },
            visitorChart() {

            },

        }

    }

</script>

<style scoped>
    .box-card-index {
        height: 16rem;
    }

    .dv-scroll-ranking-board {
        color: #0b0b0b;
    }

    .top_index_class {
        margin-top: 1.3rem;
        font-size: 20px;
        font-weight: 100;
    }
</style>
